.footer {
    width: 100%;
    padding: 60px 0 80px;
    margin-top: auto;

    background-color: $whiteDarker-color;
    border-top: 1px solid $grey-color;

    @media(max-width:1439px) {
        padding: 20px 0 30px;

        .subnav__info-text {
            font-size: 16px;
        }

        .footer__address {
            max-width: 180px;
            font-size: 16px;
        }

        .subnav__info+.subnav__info {
            // margin-top: 10px;

            @media(max-width:1023px) {
                margin-top: 0;
            }
        }

        /* .subnav__info-social {
            margin-top: 8px;
        } */
    }

    @media(max-width:1023px) {
        .subnav__info {
            margin-bottom: 26px;
        }

        .subnav__info-text {
            font-size: 18px;
        }

        .subnav__info-social {
            margin-top: 16px;
        }

        .footer__address {
            max-width: unset;
            font-size: 18px;
        }
    }
}

.footer__list {
    display: flex;

    @media(max-width:1894px) {
        justify-content: space-between;
    }

    @media(max-width:1023px) {
        flex-wrap: wrap;
    }
}

.footer__item {
    &:nth-child(1) {
        margin-right: 142px;

        @media(max-width:1894px) {
            margin-right: 25px;
        }

        @media(max-width:1023px) {
            margin: 0 auto;
            width: 291px;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }
    }

    &:nth-child(2) {
        margin-right: 145px;

        @media(max-width:1894px) {
            margin-right: unset;
        }

        @media(max-width:1023px) {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-bottom: 44px;
        }
    }

    &:nth-child(3) {
        margin-right: 181px;

        @media(max-width:1894px) {
            margin-right: unset;
        }

        @media(max-width:1023px) {
            margin: 0 auto;
            width: 100%;
            margin-bottom: 44px;
        }
    }

    &:nth-child(4) {
        margin-right: 225px;

        @media(max-width:1894px) {
            margin-right: unset;
        }
    }
}

.footer__logo {
    display: block;
    width: 154px;
    height: 88px;
    margin-bottom: 20px;

    @media(max-width:1439px) {
        margin-bottom: 10px;
    }

    @media(max-width:1023px) {
        width: 107px;
        height: 71px;
        margin: 0 92px 26px;

        img {
            object-fit: contain;
        }
    }
}

.footer__contact-link {
    display: block;
    margin-bottom: 24px;

    transition: .2s linear;

    @media(max-width:1439px) {
        font-size: 16px;
    }

    @media(max-width:1023px) {
        margin-bottom: 24px;
        font-size: 18px;
    }

    &:hover {
        color: $black-color;

        @media(min-width:1024px) {
            color: $red-color;
        }
    }
}

.footer__link {
    display: block;

    transition: .2s linear;

    &:hover {
        color: $black-color;

        @media(min-width:1024px) {
            color: $red-color;
        }
    }

    &+& {
        margin-top: 36px;

        @media(max-width:1439px) {
            margin-top: 24px;
        }
    }

    @media(max-width:1439px) {
        font-size: 16px;
    }

    @media(max-width:1023px) {
        font-size: 18px;
    }
}