.subnav {
    width: 100vw;

    background-color: $white-color;

    position: fixed;
    top: 112px;
    left: 0;
    bottom: 0;
    z-index: 2;
    // transform: translate(-50%, 0);

    @media(min-width:1024px) {
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.06);
        overflow: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

        -ms-overflow-style: none;
        /* IE and Edge */
        scrollbar-width: none;
        /* Firefox */

        display: none;
    }

    @media(max-width:1023px) {
        position: relative;
        transform: translate(0, 0);
        filter: unset;
        top: unset;
        z-index: unset;
        left: unset;
        bottom: unset;

        display: none;
        padding-top: 16px;
        margin-top: 20px;
        padding-left: 10px;
        margin-left: -10px;
        border-top: 1px solid $grey-color;

        .container {
            padding: 0;
        }
    }
}


.subnav__list {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    @media(max-width:1023px) {
        flex-direction: column;
    }
}

.subnav__item {
    max-width: 424px;
    padding: 44px 0 64px;

    @media(max-width:1439px) {
        padding: 15px 0;
        max-width: 350px;
    }

    @media(max-width:1023px) {
        padding: 0;
        max-width: unset;
    }

    &:last-child {
        margin-right: 64px;

        @media(max-width:1439px) {
            margin-right: 0;
        }
    }

    &--contacts {
        @media(max-width:1023px) {
            display: flex;
            width: 100%;
            flex-direction: column;
            align-items: center;
            order: 5;
        }
    }
}


.subnav__info {
    &+& {
        margin-top: 40px;

        @media(max-width:1439px) {
            margin-top: 18px;
        }

        @media(max-width:1023px) {
            margin-top: 0;
        }
    }

    @media(max-width:1023px) {
        margin-bottom: 40px;
    }

    &--title {
        @media(max-width:1023px) {
            display: none;
        }
    }

    &--region {
        @media(max-width:1023px) {
            display: none;
        }
    }

    &--social {
        @media(max-width:1023px) {
            order: 3;
            margin-left: 42px;
            margin-bottom: 26px;

            .subnav__info-text {
                font-weight: 400;

            }
        }
    }

    &--tel {
        @media(max-width:1023px) {
            order: 2;
            margin-bottom: 24px;

            .subnav__info-text {
                font-weight: 400;
            }
        }
    }

    &--mail {
        @media(max-width:1023px) {
            order: 3;
            margin-bottom: 24px;

            .subnav__info-link {
                font-weight: 400;
            }
        }
    }

    &--address {
        @media(max-width:1023px) {
            order: 4;
            display: flex;

            .subnav__info-text {
                font-weight: 400;

                &:first-child {
                    margin-right: 5px;
                }
            }
        }
    }

    &--contacts {
        @media(max-width:1023px) {
            order: 4;
            margin-right: 42px;
            margin-bottom: 26px;

            .subnav__info-text {
                font-weight: 400;

            }
        }
    }
}

.subnav__info-title {
    display: block;
    margin-bottom: 12px;


    @media(max-width:1023px) {
        line-height: 26px;
        margin-bottom: 12px;
    }
}

.subnav__info-text {
    @media(max-width:1023px) {
        line-height: 26px;
    }
}

.subnav-hover {
    transition: .2s linear;

    &:hover {
        @media(min-width:1024px) {
            color: $red-color;
        }
    }
}

.subnav__info-social {
    display: flex;
    align-items: center;
    margin-top: 16px;
}

.subnav__info-social-link {
    &+& {
        margin-left: 24px;
    }

    &:hover {
        @media(min-width:1024px) {
            .subnav__info-social-icon {
                fill: $red-color;
            }
        }
    }
}

.subnav__info-social-icon {
    width: 24px;
    height: 24px;
    fill: $black-color;

    transition: .2s linear;
}