.contacts {
    margin-top: 218px;
    margin-bottom: 120px;

    @media(max-width:1023px) {
        margin-top: 90px;
    }

    @media(max-width:767px) {
        margin-bottom: 0;
    }
}

.contacts__title {
    margin-bottom: 40px;

    @media(max-width:1023px) {
        margin-bottom: 32px;
    }
}

.contacts__top {
    display: flex;
    margin-bottom: 114px;
    position: relative;

    @media(max-width:1300px) {
        flex-direction: column;
    }

    @media(max-width:767px) {
        margin-bottom: 60px;
    }
}

.contacts__img {
    position: absolute;
    left: 0;
    right: 0;
    top: -213px;
    z-index: -1;

    @media(max-width:1023px) {
        display: none;
    }
}

.contacts__subtitle {
    margin-bottom: 20px;

    font-weight: 600;
    font-size: 40px;
    line-height: 48px;
    color: $red-color;

    @media(max-width:1023px) {
        font-size: 26px;
        line-height: 38px;

        margin-bottom: 12px;
    }
}

.contacts__text {
    margin-bottom: 40px;
    max-width: 529px;

    @media(max-width:1023px) {
        margin-bottom: 24px;
        max-width: 359px;
    }
}

.contacts__group {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    max-width: 632px;

    @media(max-width:767px) {
        flex-direction: column;
        align-items: center;
        max-width: unset;
    }
}

.contacts__textarea-wrapper {
    display: block;
    width: fit-content;
    margin-bottom: 40px;
    height: auto;
    border-bottom: 1px solid $black-color;

    position: relative;

    @media(max-width:767px) {
        width: 276px;
        margin: 0 auto 40px;
    }
/* 
    &::before {
        content: '';
        width: 10px;
        height: 10px;
        background-image: url('../img/textarea.svg');

        position: absolute;
        right: 0;
        bottom: 3px;
        z-index: 2;
        pointer-events: none;

        @media(max-width:767px) {
            display: none;
        }
    } */
}

.contacts__textarea {
    display: block;
    width: 642px;
    min-height: 66px;

    border: none;
    border-radius: 0;
    background-color: transparent;
    resize: none;

    @media(max-width:767px) {
        width: 276px;
        min-height: 84px;
        resize: none;
    }

    &::placeholder {
        font-weight: 300;
        font-size: 16px;
        line-height: 24px;
        color: $black-color;
    }

    &:hover::placeholder {
        @media(min-width:1024px) {
            color: $grey-color;
            font-weight: 400;
        }
    }

    &:focus {
        color: $black-color;
        font-weight: 400;
    }

    &:focus::placeholder {
        color: $black-color;
        font-weight: 400;
    }
}

.contacts__info {
    display: flex;
    margin-top: 68px;
    margin-left: 395px;

    @media(max-width:1650px) {
        margin-left: auto;
    }

    @media(max-width:1300px) {
        margin: 40px auto 0;
    }

    @media(max-width:767px) {
        flex-direction: column-reverse;
        align-items: center;
    }
}

.contacts__info-left {
    max-width: 212px;
    margin-right: 84px;

    @media(max-width:767px) {
        margin-right: 0;
        margin-top: 26px;
        max-width: unset;
    }
}

.contacts__info-region {
    margin-bottom: 58px;

    @media(max-width:767px) {
        display: none;
    }
}

.contacts__info-tel {
    margin-bottom: 57px;

    @media(max-width:767px) {
        margin-bottom: 24px;
        display: flex;
        justify-content: center;
    }
}

.contacts__info-mail {
    margin-bottom: 42px;

    @media(max-width:767px) {
        margin-bottom: 24px;
        display: flex;
        justify-content: center;
    }
}

.contacts__info-address {
    @media(max-width:767px) {
        display: inline;
    }
}

.contacts__info-social+.contacts__info-social {
    margin-top: 36px;

    @media(max-width:767px) {
        margin-left: 60px;
        margin-right: 34px;
        margin-top: 0;
    }
}

.contacts__info-social {
    @media(max-width:767px) {
        margin-left: 34px;
    }
}

.contacts__info-right {
    @media(max-width:767px) {
        display: flex;
    }
}

.contacts__map {
    height: 498px;
}

.p {
    background: lime;
    height: 200px;
    width: 300px;
    margin: 40px auto;
}

.p.resizable {
    background: cyan;
    position: relative;
}

.p .resizer {
    width: 10px;
    height: 10px;
    background: blue;
    position: absolute;
    right: 0;
    bottom: 0;
    cursor: se-resize;
}