.request {
    display: flex;
    align-items: flex-start;
    // margin-bottom: 120px;
    margin-bottom: 120px;
    margin-top: 218px;

    @media(max-width:1710px) {
        margin-top: 150px;
        margin-bottom: 80px;
    }

    @media(max-width:1439px) {
        margin-bottom: 40px;
        margin-top: 130px;
    }

    @media(max-width:1023px) {
        margin-top: 0;
        margin-bottom: 60px;
    }
}

.request-section {
    margin-bottom: 120px;

    @media(max-width:1439px) {
        margin-bottom: 120px;
    }

    @media(max-width:1023px) {
        margin-bottom: 60px;
    }
}

.request-section__inner {
    display: flex;
    margin-left: 69px;

    @media(max-width:1599px) {
     margin-left: 0;
    }

    @media(max-width:1023px) {
        flex-direction: column;
        margin-left: 0;
    }
}

.request__left {
    margin-right: 20px;

    @media(max-width:1023px) {
        margin-right: 0;
    }
}

.request__title {
    max-width: 839px;
    margin-bottom: 35px;

    @media(max-width:1919px) {
        line-height: 52px;
        max-width: 750px;
        margin-bottom: 25px;

        span {
            line-height: inherit;
            font-size: inherit;
        }
    }

    @media(max-width:1365px) {
        @media(min-width:1024px) {
            font-size: 33px;
            line-height: 42px;
            max-width: 670px;
        }
    }

    @media(max-width:1023px) {
        margin-bottom: 36px;
    }

    @media(max-width:425px) {
        span {
            display: block;
        }
    }
}

.request__phrase {
    width: 868px;
    height: 109px;

    @media(max-width:1919px) {
        width: 700px;
        height: auto;
    }

    @media(max-width:1439px) {
        width: 620px;
    }

    @media(max-width:1365px) {
        width: 480px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.request__right-title {
    margin-bottom: 20px;

    font-weight: 600;
    font-size: 40px;
    line-height: 48px;
    color: $red-color;

    @media(max-width:1439px) {
        margin-bottom: 0;
    }

    @media(max-width:1365px) {
        font-size: 30px;
        line-height: 38px;
    }

    @media(max-width:1023px) {
        margin-bottom: 12px;
        font-size: 26px;
        margin-bottom: 12px;
    }
}

.request__right-text {
    max-width: 529px;
    margin-bottom: 40px;

    @media(max-width:1439px) {
        margin-bottom: 10px;
        line-height: 20px;

        @media(min-width:1024px) {
            max-width: 419px;
        }
    }


    @media(max-width:1365px) {
        font-size: 16px;
    }

    @media(max-width:1023px) {
        margin-bottom: 24px;
    }
}