.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 225px;
    height: 44px;

    background: #A9081C;
    box-shadow: 0px 4px 12px rgba(152, 33, 44, 0.24);
    transition: .2s linear;

    font-weight: 450;
    font-size: 16px;
    line-height: 24px;
    color: $white-color;

    &:hover {
        color: $white-color;

        @media(min-width:1024px) {
            background: #C5162C;
        }
    }

    &:active {
        font-weight: 400;
        box-shadow: none;
    }

    &.no-active {
        background-color: $white-color;
        border: 1px solid $red-color;
        box-shadow: none;
        pointer-events: none;

        color: #CDCDCD;
    }
}