.company {
    
    &--slider {
        max-width: 1756px;
        @media(max-width:1023px) {
            margin-top: 0;
            margin-bottom: 60px;
        }
    }

    &--not-slider {
        max-width: 1920px;
        margin-bottom: 120px;
        padding-bottom: 60px;
        position: relative;

        @media(max-width:1439px) {
            margin-bottom: 40px;
        }

        @media(max-width:1023px) {
            margin-bottom: 60px;
            padding-bottom: 0;
        }


        @media(min-width:1024px) {
            @media(max-width:1919px) {
                .company__title {
                    line-height: 62px;

                    span {
                        line-height: inherit;
                    }
                }

                .company__item {
                    height: 160px;
                    margin-bottom: 20px;
                }

                .company__img {
                    img {
                        max-height: 100px;
                        width: 100%;
                        height: auto;
                        object-fit: contain;
                    }
                }

                .company__btn {
                    margin-top: 20px;
                }

                .company__phrase {
                    height: 227px;
                    bottom: -142px;
                }
            }

            @media(max-width:1439px) {
                .company__title {
                    line-height: 52px;
                }

                .company__list {
                    margin: 0 -30px -10px;
                }

                .company__item {
                    width: calc(25% - 60px);
                    height: 130px;
                    margin: 0 30px 10px;
                }

                .company__img {
                    img {
                        max-height: 80px;
                        width: 100%;
                        height: auto;
                        object-fit: contain;
                    }
                }

                .company__descr {
                    bottom: 5px;
                }

                @media(max-width:1100px) {
                    .company__list {
                        margin: 0 -10px -10px;
                    }

                    .company__item {
                        width: calc(25% - 20px);
                        height: 130px;
                        margin: 0 10px 10px;
                    }

                    .company__phrase {
                        height: 147px;
                        bottom: -72px;
                    }
                }
            }

            @media(max-width:1365px) {
                .company__title {
                    font-size: 35px;
                    line-height: 42px;

                    span {
                        font-size: inherit;
                        line-height: 42px;
                    }
                }
            }
        }
    }
}

.company__title {
    width: 100%;
    margin-bottom: 12px;

    span {
        color: $red-color;
    }

    @media(max-width:1023px) {
        margin-bottom: 16px;
    }
}

.company__subtitle {
    margin-bottom: 36px;

    @media(max-width:414px) {
        max-width: 328px;
    }
}

.company__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 -10px -10px;

    position: relative;

    @media(max-width:1023px) {
        margin: 0;
        flex-direction: column;
        align-items: center;
    }
}

.company__item {
    width: calc(100% / 4 - 20px);
    margin: 0 10px 40px;
    height: 200px;

    @media(max-width:1773px) {
        height: 200px;
    }

    @media(max-width:1439px) {
        height: 170px;
    }

    @media(max-width:1023px) {
        width: 359px;
        height: 206px;
        margin: 0 0 40px;

        &:nth-child(1n + 5) {
            display: none;
        }
    }
}

.company__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    background: #FFFFFF;

    position: relative;

    &::before {
        content: '';
        box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
        opacity: 0;

        transition: .2s linear;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;

        @media(max-width:1023px) {
            opacity: 1;
            z-index: 1;
        }
    }

    &:hover {
        @media(min-width:1024px) {
            &::before {
                opacity: 1;
            }

            .company__descr {
                opacity: 1;
            }
        }
    }
}

.company__img {
    margin-top: -20px;
    max-width: 40%;

    img {
        max-height: 120px;
        width: 100%;
        height: auto;
        object-fit: contain;
    }

    @media(max-width:1023px) {
        max-width: 33%;
    }
}

.company__descr {
    padding: 0 15px;
    text-align: center;

    opacity: 0;
    transition: .2s linear;

    position: absolute;
    left: 0;
    right: 0;
    bottom: 16px;

    @media(max-width:1023px) {
        opacity: 1;
        bottom: 10px;
    }
}

.company__phrase {
    height: 287px;

    position: absolute;
    left: -112px;
    right: -131px;
    bottom: -82px;
    z-index: -1;

    img {
        object-fit: unset;
    }

    @media(min-width:1921px) {
        right: unset;
        width: 1999px;

        /* width: 1999px;
        left: 50%;
        right: unset;
        transform: translateX(-50%);
        height: auto;
        bottom: -122px; */
    }

    @media(max-width:1440px) {
        height: auto;
        left: -37px;
        right: -50px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.company__btn {
    margin: 0 auto;
}