.profesional {
    display: flex;
    align-items: center;
    position: relative;

    @media(max-width:1023px) {
        flex-direction: column-reverse;
        margin-top: 0;
        margin-bottom: 60px;
    }
}

.profesional__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -14px -28px;
    max-width: 896px;

    @media(max-width:1750px) {
        max-width: 700px;
    }

    @media(max-width:1439px) {
        max-width: 400px;
        justify-content: center;
    }
}

.profesional__item {
    width: 196px;
    height: 196px;
    margin: 0 14px 28px;

    @media(max-width:1750px) {
        &:nth-child(1n + 7) {
            display: none;
        }
    }

    @media(max-width:1439px) {
        width: 166px;
        height: 166px;

        &:nth-child(1n + 5) {
            display: none;
        }
    }

    @media(max-width:1023px) {
        width: 172px;
        height: 172px;
        margin: 0 7.5px 24px;

    }
}

.profesional__img {
    position: relative;
    z-index: 1;
    filter: drop-shadow(0px 5px 20px rgba(0, 0, 0, 0.1));


    transition: .3s linear;

    &:hover {
        @media(min-width:1024px) {
            transform: scale(2);
            z-index: 2;

            @media(max-width:1685px) {
                transform: scale(1.5);
            }
        }
    }
}

.profesional__info {
    margin-left: 168px;

    @media(max-width:1685px) {
        margin-left: 40px;
    }

    @media(max-width:1023px) {
        margin-left: 0;
    }
}

.profesional__title {
    max-width: 572px;
    margin-bottom: 40px;

    @media(max-width:1919px) {
        max-width: 36vw;
    }

    @media(max-width:1023px) {
        margin-bottom: 16px;
        max-width: 48vw;
    }

    @media(max-width:767px) {
        max-width: 455px;
    }

    @media(max-width:668px) {
        max-width: 432px;
    }

    @media(max-width:579px) {
        max-width: 412px;
    }

    @media(max-width:502px) {
        max-width: 389px;
    }

    @media(max-width:414px) {
        max-width: 367px;
    }
}

.profesional__text {
    max-width: 486px;

    @media(max-width:1023px) {
        margin-bottom: 36px;
    }
}

.profesional__btn {
    display: none;

    @media(max-width:1023px) {
        display: flex;
        margin: 50px auto 0;
    }
}

.profesional__phrase {
    width: 1756px;
    height: 175px;

    position: absolute;
    left: 0;
    right: 0;
    top: calc(100% + 24px);
    z-index: -1;

    @media(max-width:1850px) {
        width: 90vw;
        height: auto;
    }
}