.development {
    margin-top: 218px;

    position: relative;

    @media(max-width:1439px) {
        margin-top: 168px;
    }

    @media(max-width:1023px) {
        margin-top: 100px;
    }
}

.development__inner {
    margin-bottom: 180px;

    @media(max-width:1023px) {
        margin-bottom: 60px;
    }
}

.development__title {
    margin-bottom: 80px;

    @media(max-width:1439px) {
        margin-bottom: 50px;
    }

    @media(max-width:1023px) {
        margin-bottom: 24px;
    }
}

.development__subtitle {
    margin-bottom: 40px;

    @media(max-width:1023px) {
        margin-bottom: 24px;
    }
}

.development__info {
    display: flex;

    @media(max-width:1023px) {
        flex-direction: column;
    }
}

.development__text-left {
    max-width: 868px;

    @media(max-width:1023px) {
        max-width: unset;
    }
}

.development__text-right {
    max-width: 572px;
    margin-left: 168px;

    @media(max-width:1439px) {
        margin-left: 38px;
    }

    @media(max-width:1023px) {
        margin-left: 0;
        margin-top: 24px;
        max-width: unset;
    }
}

.development__phrase {
    position: absolute;
    left: 0;
    right: 0;
    z-index: -1;

    &--one {
        top: 54px;
        height: 378px;

        @media(max-width:1023px) {
            display: none;
        }
    }

    &--two {
        top: 474px;
        height: 332px;

        @media(max-width:1023px) {
            display: none;
        }
    }

    img {
        object-fit: unset;
    }
}