.construction {
    background-color: $whiteDarker-color;
    margin-bottom: 180px;

    @media(max-width:1439px) {
        margin-bottom: 120px;
    }

    @media(max-width:1023px) {
        margin-bottom: 60px;
    }
}

.construction__inner {
    padding: 80px 0;
    position: relative;

    @media(max-width:1023px) {
        padding: 44px 0;
    }
}

.construction__title {
    margin-bottom: 40px;

    @media(max-width:1023px) {
        margin-bottom: 16px;
    }

    @media(max-width:414px) {
        font-size: 28px;
        line-height: 42px;
    }
}

.construction__text {
    margin-bottom: 40px;
    max-height: 150px;
    max-width: 1312px;

    transition: .3s linear;
    overflow: hidden;

    position: relative;

    @media(max-width:600px) {
        max-height: 286px;
        margin-bottom: 16px;
    }

    &::before {
        content: '';
        height: 80px;
        background: linear-gradient(0deg, #FBFBFB 0%, rgba(251, 251, 251, 0) 100%);
        opacity: 1;

        transition: .3s linear;

        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
    }

    &.active::before {
        opacity: 0;
    }
}


.construction__btn {
    position: relative;
    overflow: hidden;

    &::before {
        content: '';
        height: 1px;
        background-color: $red-color;

        transition: .2s linear;
        transform: translateX(-100%);

        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
    }

    &:hover {
        color: $red-color;

        &::before {
            @media(min-width:1024px) {
                transform: translateX(0);
            }
        }
    }

    &.active {
        .construction__btn-open {
            display: none;
        }

        .construction__btn-close {
            display: block;
        }
    }
}

.construction__btn-open {
    color: $red-color;

    display: block;
}

.construction__btn-close {
    color: $red-color;
    display: none;
}