.select {
    margin-bottom: 16px;

    position: relative;
    background: $whiteDarker-color;

    &.active {
        z-index: 120;

        .select__content {
            z-index: 120;
        }
    }
}

.select__top {
    position: relative;

    border: 0.8px solid $grey-color;
    background: $whiteDarker-color;

    display: block;
    width: 410px;
    height: 42px;
    padding: 9px 20px;

    cursor: pointer;

    @media(max-width:1023px) {
        width: 359px;
    }

    &::before {
        content: url();
        width: 10px;
        height: 5px;

        background-image: url('../img/small-arrow.svg');
        background-size: cover;
        background-repeat: no-repeat;

        position: absolute;
        right: 23px;
        top: 50%;
        transform: translateY(-50%) rotate(180deg);
        z-index: 2;

        transition: .3s linear;
    }

    &.active::before {
        transform: translateY(-50%) rotate(0);
    }
}

.select__top-title {
    pointer-events: none;
}

.select__content {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 22;

    display: none;
    padding: 10px 0;

    background: #FFFFFF;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.06);

    &::-webkit-scrollbar {
        display: none;
    }

    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
}


.select__input {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;

    position: relative;
    transition: all .3s;

    &:hover {
        .select__item {
            background-color: $whiteDarker-color;
        }
    }

    cursor: pointer;

    &:last-child {
        border-bottom: none;
    }

    input {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        opacity: 0;
        cursor: pointer;

        &:checked+.select__item {}
    }
}

.select__item {
    display: block;
    width: 100%;
    padding: 6px 40px;
}