.overlay {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;

    width: 100%;
    height: 100%;
    z-index: 100;

    background: rgba(0, 0, 0, 0.3);
    opacity: 0;
    visibility: hidden;

    transition: .2s linear;

    &.active {
        opacity: 1;
        visibility: visible;
    }
}

.popup {
    width: 100%;
    max-width: 720px;
    padding: 0 20px 24px;

    background: #FFFFFF;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.06);
    opacity: 0;
    visibility: hidden;

    position: fixed;
    top: 50%;
    left: 50%;
    z-index: 101;
    transform: translate(-50%, -50%);

    transition: .2s linear;

    &::before {
        content: '';
        height: 1px;
        background-image: url("../img/line.svg");

        position: absolute;
        left: 20px;
        right: 20px;
        bottom: 24px;
    }

    &.active {
        opacity: 1;
        visibility: visible;
    }

    &--region {
        padding-top: 17px;

        @media(max-width:767px) {
            width: 359px;
            height: 478px;
            top: 120px;
            transform: translate(-50%, 0);
            padding: 14px 10px 24px;

            &::before {
                left: 10px;
                right: 10px;
            }
        }
    }

    &--call {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 570px;
        padding-top: 44px;

        @media(max-width:1919px) {
            height: 540px;
            max-width: 610px;
        }

        @media(max-width:767px) {
            width: 359px;
            height: 548px;
            justify-content: flex-start;

            padding: 14px 12px 24px;

            &::before {
                left: 10px;
                right: 10px;
            }
        }

    }

    &--calculate {
        width: 1460px;
        height: 762px;
        max-width: unset;
        padding-top: 44px;
        padding: 60px 0 60px 148px;


        display: flex;
        align-items: center;
        justify-content: center;

        top: 122px;
        transform: translate(-50%, 0);
        overflow: hidden;

        &::before {
            display: none;
        }

        @media(max-width:1919px) {
            max-width: 1460px;
            width: 90vw;
            height: auto;
            padding: 20px 0 20px 78px;

            top: 50%;
            transform: translate(-50%, -50%);
        }

        @media(max-width:1439px) {
            max-width: unset;
            padding: 20px;
        }

        @media(max-width:1023px) {
            top: 76px;
            align-items: flex-start;
            min-width: 359px;
            padding: 68px 8px 24px;
            height: calc(var(--vh, 1vh) * 80);
            transform: translate(-50%, 0);

            &.quiz-start {
                padding: 14px 8px 24px;
            }
        }

        @media(max-width:375px) {
            width: 100vw;
        }
/* 
        @media(min-width:1921px) {
            width: 1860px;
            height: 862px;
            top: 50%;
            transform: translate(-50%, -50%);
        } */

    }

    &--thanks {
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: unset;
        width: 868px;
        height: 581px;
        padding-top: 44px;

        &::before {
            display: none;
        }

        @media(max-width:1919px) {
            width: 758px;
            height: 481px;

            .popup--thanks__title {
                font-size: 70px;
                line-height: 70px;
            }

            .popup--thanks__text {
                font-size: 26px;
                line-height: 36px;
                max-width: 390px;
            }
        }

        @media(max-width:1439px) {
            width: 578px;
            height: 461px;

            .popup--thanks__phrase {
                width: 440px;
                height: 57px;
            }

            .popup--thanks__title {
                font-size: 60px;
                line-height: 60px;
            }

            .popup--thanks__text {
                font-size: 24px;
                line-height: 32px;
                max-width: 360px;
            }
        }

        @media(max-width:1023px) {
            align-items: center;
            justify-content: center;
            width: 375px;
            height: calc(var(--vh, 1vh) * 80);
            min-width: 359px;
            padding: 45px 12px;
        }


    }
}

.popup__close {
    width: 24px;
    height: 24px;
    padding: 5px;

    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 10;

    svg {
        transition: .2s linear;

        fill: $black-color;
    }

    &:hover svg {
        @media(min-width:1024px) {
            fill: $red-color;
        }
    }
}

.popup__title {
    margin-bottom: 60px;
    text-align: center;



    @media(max-width:767px) {
        font-size: 20px;
        line-height: 30px;
    }
}

.popup--call-message {
    display: none;

    &.show {
        display: block;
    }
}

.popup__message {
    max-width: 424px;
    margin-bottom: 36px;

    text-align: center;
}

.popup__icon {
    width: 54px;
    height: 54px;
    margin: 0 auto;
}

.popup--thanks__phrase {
    width: 600px;
    height: 67px;

    img {
        object-fit: unset;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.popup--thanks__title {
    margin-bottom: 8px;
}

.popup--thanks__img {
    width: 86px;
    height: 86px;
    margin-bottom: 8px;
}

.popup--thanks__text {
    max-width: 530px;
    text-align: center;
    color: $black-color;
}