.reviews {
    display: flex;

    @media(max-width:1023px) {
        flex-direction: column;
        margin-bottom: 60px;
    }

    @media(min-width:1921px) {
        position: relative;
    }

}

.reviews-block {
    margin-bottom: 80px;

    @media(max-width:1440px) {
        margin-bottom: 10px;
    }


    @media(min-width:1024px) {

        @media(max-width:1919px) {
            .reviews__phrase {
                bottom: 32px;
            }

            .reviews__img {
                width: 256px;
                height: 330px;
            }

            .reviews__title {
                margin-bottom: 20px;
                line-height: 62px;

                span {
                    line-height: inherit;
                    font-size: inherit;
                }
            }
        }

        @media(max-width:1439px) {
            .reviews__img {
                width: 228px;
                height: 290px;
            }

            .reviews__title {
                margin-bottom: 16px;
                line-height: 52px;
            }

            .reviews__name {
                margin-bottom: 0;
            }

            .reviews__text {
                margin-bottom: 20px;
            }
        }


        @media(max-width:1365px) {
            .reviews__img {
                width: 160px;
                height: 210px;
                margin-left: auto;
                margin-right: auto;
            }

            .reviews__title {
                line-height: 42px;
                font-size: 35px;
            }

            .reviews__text {
                font-size: 18px;
            }

            .reviews__descr {
                font-size: 14px;
                line-height: 19px;
                text-align: center;
            }

            .reviews__phrase {
                bottom: 82px;
            }
        }
    }
}

.reviews-block__inner {
    display: flex;
    position: relative;
    padding-bottom: 312px;

    @media(max-width:1440px) {
        padding-bottom: 222px;
    }

    @media(max-width:1023px) {
        flex-direction: column;
        padding-bottom: 0;
        margin-bottom: 60px;
    }

    .reviews__phrase {
        height: 232px;
        width: auto;

        top: unset;
        left: 0;
        right: 0;
        bottom: 0;
    }
}

.reviews__left {
    margin-right: 30px;

    @media(max-width:1023px) {
        margin-left: 0;
    }
}

.reviews__title {
    max-width: 710px;
    margin-bottom: 40px;

    @media(max-width:1023px) {
        margin-bottom: 16px;
    }
}

.reviews__text {
    max-width: 428px;
    margin-bottom: 40px;

    @media(max-width:1023px) {
        margin-bottom: 36px;
    }
}

.reviews__btn {
    @media(max-width:1023px) {
        margin: 0 auto;
    }
}

.reviews__list {
    display: flex;

    @media(max-width:1023px) {
        flex-direction: column;
        align-items: center;
    }
}

.reviews__item {
    margin: 0 10px;

    @media(max-width:1023px) {
        margin: 0 0 40px;

        &:last-child {
            display: none;
        }
    }
}


.reviews__img {
    width: 276px;
    height: 360px;
    margin-bottom: 20px;

    background: #FBFBFB;
    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);

    transition: transform .4s linear;

    @media(min-width:2150px) {
        width: 376px;
        height: 460px;
    }

    @media(max-width:1439px) {
        width: 160px;
        height: 210px;
        margin-left: auto;
        margin-right: auto;
    }

    @media(max-width:1023px) {
        width: 277px;
        height: 371px;
        margin-bottom: 16px;
    }

    @media(min-width:1024px) {
        &:hover {
            transform: scale(2.05) translateY(65px);

            @media(max-width:1600px) {
                transform: scale(1.4) translateY(50px);
            }
        }
    }

    @media(min-width:2150px) {
        &:hover {
            transform: scale(1.3) translateY(40px);

        }
    }

    img {
        object-fit: contain;
    }
}

.reviews__name {
    margin-bottom: 12px;

    @media(max-width:1439px) {
        font-size: 18px;
        line-height: 26px;
        text-align: center;
    }

    @media(max-width:1023px) {
        text-align: start;
        margin-bottom: 20px;
    }
}

.reviews__descr {
    max-width: 276px;

    @media(max-width:1439px) {
        font-size: 18px;
        line-height: 26px;
        text-align: center;
    }

    @media(max-width:1023px) {
        text-align: start;
    }
}

.reviews__phrase {
    height: 193px;
    width: 1460px;

    position: absolute;
    right: 0;
    bottom: 82px;
    z-index: -1;

    img {
        object-fit: unset;
    }

    @media(min-width:2150px) {
        right: unset;
        left: 460px;
        bottom: 15px;
    }

    @media(max-width:1539px) {
        width: 90vw;
        height: auto;
        bottom: -10px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}


.reviews-section {
    padding-bottom: 120px;
    margin-top: 218px;

    @media(max-width:1439px) {
        margin-top: 168px;
    }

    @media(max-width:1023px) {
        margin-top: 110px;
        padding-bottom: 40px;
    }
}

.reviews-section__title {
    margin-bottom: 80px;

    @media(max-width:1439px) {
        font-size: 40px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.reviews-section__list {
    margin-bottom: 40px;
    border-bottom: 1px solid rgba($black-color, .2);
}

.reviews-section__item {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0 148px;
    margin-bottom: 80px;

    @media(max-width:1850px) {
        padding: 0;
    }

    @media(max-width:1439px) {
        margin-bottom: 60px;
    }

    @media(max-width:1023px) {
        flex-direction: column;
        align-items: center;
        padding-bottom: 60px;
        border-bottom: 1px solid rgba($black-color, .2);

        &:last-child {
            padding-bottom: 0;
            border-bottom: none;
        }
    }

    @media(min-width:1024px) {

        &:first-child {
            padding-bottom: 80px;

            border-bottom: 1px solid rgba($black-color, .2);

            @media(max-width:1439px) {
                padding-bottom: 60px;
            }
        }

        &:nth-child(even) {
            flex-direction: row-reverse;
        }
    }
}

.reviews-section__img {
    width: 424px;
    height: 548px;

    background: $grey-color;
    filter: drop-shadow(0px 4px 14px rgba(0, 0, 0, 0.08));
    transition: transform .3s linear;

    @media(max-width:1599px) {
        width: 362.39px;
        height: 467.39px;
    }

    @media(max-width:1023px) {
        margin-bottom: 36px;
    }

    @media(min-width:1024px) {
        &:hover {
            transform: scale(1.2);
        }
    }
}

.reviews-section__info {
    @media(max-width:1439px) {
        max-width: 470px;
    }

    @media(max-width:1023px) {
        max-width: unset;
    }
}

.reviews-section__top {
    display: flex;
    margin-bottom: 60px;

    @media(max-width:1439px) {
        align-items: center;
        margin-bottom: 24px;
    }
}

.reviews-section__logo {
    width: 128px;
    height: 128px;
    margin-right: 20px;

    background: #FFFFFF;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.04);

    @media(max-width:1439px) {
        width: 86px;
        height: 86px;
    }
}

.reviews-section__name {
    margin-bottom: 24px;


    @media(max-width:1439px) {
        font-size: 22px;
        line-height: 32px;
        margin-bottom: 12px;
    }
}

.reviews-section__company {
    @media(max-width:1439px) {
        font-size: 20px;
        line-height: 30px;
    }
}

.reviews-section__text {
    max-width: 868px;

    @media(max-width:1439px) {
        font-size: 18px;
        line-height: 26px;
    }
}

.reviews-section__btn {
    margin: 0 auto;
}