.header {
    width: 100%;
    padding: 24px 0;
    background-color: $white-color;

    transition: .2s linear;

    position: fixed;
    top: 0;
    z-index: 100;

    @media(max-width:1023px) {
        padding: 6px 0;
        // position: relative;
    }

    &.active,
    &.hide-scroll {
        @media(min-width:1024px) {
            padding: 12px 0;
        }

        .header__inner::before {
            box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.04);

            @media(min-width:1024px) {
                height: 92px;
            }
        }

        .header__logo {
            @media(min-width:1024px) {
                width: 119px;
                height: 68px;
            }
        }

        .nav {
            @media(min-width:1024px) {
                height: 68px;
                margin-left: 177px;

                @media(max-width:1320px) {
                    margin-left: 105px;
                }
            }

        }

        .subnav {
            @media(min-width:1024px) {
                top: 80px;
            }
        }

    }
}

.header__inner {
    display: flex;
    align-items: center;

    background-color: $white-color;

    position: relative;
    z-index: 2;

    @media(max-width:1023px) {
        justify-content: space-between;
        // position: static;
    }

    &::before {
        content: '';
        height: 136px;
        width: 100vw;

        background-color: white;

        position: absolute;
        top: 50%;
        left: 50%;
        z-index: -1;
        transform: translate(-50%, -50%);
        transition: .2s linear;

        @media(max-width:1023px) {
            height: 56px;
        }
    }

    &.active::before {
        @media(max-width:1023px) {
            box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.04);
        }
    }
}

.header__logo {
    display: block;
    width: 154px;
    height: 88px;

    transition: .2s linear;

    @media(min-width:1024px) {
        order: 1;
    }

    @media(max-width:1023px) {
        width: 77px;
        height: 44px;
    }
}

.header__info {
    margin-left: 134px;

    @media(min-width:1024px) {
        order: 4;
    }

    @media(max-width:1320px) {
        margin-left: 55px;
    }

    @media(max-width:1023px) {
        margin-left: 0;
    }
}

.header__link {
    display: block;

    font-weight: 450;
    font-size: 20px;
    line-height: 30px;

    @media(max-width:1023px) {
        font-size: 18px;
        line-height: 26px;
    }
}

.header__btn-call {
    display: block;
    margin-left: auto;

    color: $red-color;
    font-weight: 300;
    font-size: 14px;
    line-height: 18px;

    position: relative;

    @media(max-width:1023px) {
        margin: 0 auto;
    }

    &::before {
        content: '';
        width: 0;
        height: 1px;

        background-color: $red-color;
        transition: .2s linear;

        position: absolute;
        left: 0;
        right: auto;
        bottom: 0;
    }

    &:hover::before {
        @media(min-width:1024px) {
            width: 100%;
        }
    }
}

.burger {
    display: none;
    width: 28px;
    height: 20px;
    margin-right: 8px;
    order: 5;

    @media(max-width:1023px) {
        display: block;
    }

    &.active {
        .burger__line {
            background-color: transparent;

            &::before {
                top: 0;
                transform: rotate(45deg);
            }

            &::after {
                bottom: 0;
                transform: rotate(-45deg);
            }
        }
    }
}

.burger__line {
    height: 4px;
    margin: 0 auto;
    background-color: $red-color;
    transition: .2s linear;

    position: relative;



    &::before {
        content: '';
        height: 4px;
        width: 28px;
        background-color: $black-color;
        transition: .2s linear;

        position: absolute;
        top: calc(100% + 4px);
        left: 0;
        right: 0;
    }

    &::after {
        content: '';
        height: 4px;
        width: 28px;
        background-color: $black-color;
        transition: .2s linear;

        position: absolute;
        bottom: calc(100% + 4px);
        left: 0;
        right: 0;
    }
}