.form--popup {
    width: 100%;
    max-width: 276px;
    margin: 0 auto;

    @media(max-width:767px) {
        max-width: unset;
    }

    &.hide {
        display: none;
    }
}

.form--section {
    @media(max-width:1023px) {
        max-width: 359px;
        display: flex;
        flex-direction: column;
        margin: 0 auto;
    }
}

.form__input {
    padding-bottom: 4px;

    border: none;
    border-radius: 0;
    border-bottom: 1px solid $black-color;

    transition: .2s linear;

    &--popup {
        display: block;
        width: 100%;
        margin-bottom: 40px;

        @media(max-width:767px) {
            margin: 0 auto 24px;
            width: 275px;
        }

        &:first-child {
            margin-bottom: 36px;
        }
    }

    &--request {
        display: block;
        width: 276px;

        @media(max-width:1439px) {
            width: 246px;
        }

        @media(max-width:1365px) {
            width: 186px;
        }

        &+& {
            margin-left: 80px;

            @media(max-width:1439px) {
                margin-left: 30px;
            }

            @media(max-width:1023px) {
                margin-left: 0;
                margin-top: 36px;
            }
        }
    }

    &--contacts {
        display: block;
        width: 276px;
        margin-bottom: 40px;
        background-color: transparent;

        @media(max-width:767px) {
            margin-bottom: 31px;
        }
    }

    &::placeholder {
        font-weight: 300;
        color: $black-color;
    }

    &:hover::placeholder {
        @media(min-width:1024px) {
            color: $grey-color;
            font-weight: 400;
        }
    }

    &:focus {
        color: $black-color;
        font-weight: 400;
    }

    &:focus::placeholder {
        color: $black-color;
        font-weight: 400;
    }

    &.error {
        font-weight: 400;
        color: $red-color;

        border-bottom: 1px solid $red-color;

        &::placeholder {
            color: $red-color;
        }
    }
}

.form__text {
    margin-bottom: 20px;

    @media(max-width:1365px) {
     margin-bottom: 10px;
    }

    @media(max-width:1023px) {
        margin-bottom: 16px;
    }

    @media(max-width:767px) {
        width: 335px;
    }
}

.form__checkbox-group {
    display: flex;
    align-items: center;
    margin-bottom: 40px;

    @media(max-width:1439px) {
        margin-bottom: 20px;
    }

    @media(max-width:1023px) {
        flex-wrap: wrap;
        max-width: 273px;
        justify-content: space-between;
        margin-bottom: 24px;
    }

    /* @media(max-width:443px) {
        max-width: 273px;
        justify-content: space-between;
        margin-bottom: 24px;
    } */
}

.form__checkbox {
    cursor: pointer;

    @media(max-width:1023px) {
        margin-bottom: 16px;
    }

    &+& {
        margin-left: 36px;

        @media(max-width:1439px) {
            margin-left: 12px;
        }

        @media(max-width:1023px) {
            margin-left: 0;
        }

        @media(max-width:443px) {
            margin-left: 0;
        }
    }


    &:hover {
        .no-pressed {
            fill: $red-color;
        }
    }
}

.form__checkbox-input {
    display: none;

    &:checked+.form__checkbox-text {
        .form__checkbox-fake {
            &--no-pressed {
                opacity: 0;
            }

            &--pressed {
                opacity: 1;
            }
        }
    }
}

.form__checkbox-text {
    padding-left: 32px;

    white-space: nowrap;

    position: relative;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
}

.form__checkbox-fake {
    width: 18px;
    height: 18px;

    position: absolute;
    left: 3px;
    top: 50%;
    transform: translateY(-50%);

    transition: .2s linear;

    svg {
        width: 18px;
        height: 18px;
    }

    .no-pressed {
        fill: $grey-color;
        transition: .2s linear;
    }

    &--no-pressed {
        opacity: 1;
    }

    &--pressed {
        opacity: 0;
    }
}

.form__btn-popup {
    margin-bottom: 16px;

    @media(max-width:767px) {
        margin: 0 auto 24px;
    }
}

.form__info-popup {
    min-width: 326px;
    margin-bottom: 56px;

    color: $grey-color;

    @media(max-width:767px) {
        text-align: center;
    }
}

.form__link {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;

    color: $red-color;

    position: relative;

    &::before {
        content: '';
        height: 1px;
        width: 0;
        background-color: $red-color;

        transition: .2s linear;

        position: absolute;
        left: 0;
        right: auto;
        bottom: 0;
    }

    &:hover {
        color: $red-color;

        &::before {
            @media(min-width:1024px) {
                width: 100%;
            }
        }
    }
}


.form__input-group {
    display: flex;

    margin-bottom: 40px;

    @media(max-width:1439px) {
        margin-bottom: 20px;
    }

    @media(max-width:1365px) {}

    @media(max-width:1023px) {
        flex-direction: column;
        align-items: center;
        margin-bottom: 24px;
    }
}

.form__bottom {
    display: flex;
    align-items: center;

    @media(max-width:1023px) {
        flex-direction: column;
        align-items: center;
    }
}

.form__info {
    margin-left: 24px;
    max-width: 326px;

    color: $grey-color;

    @media(max-width:1365px) {
        margin-left: 10px;

        @media(min-width:1024px) {
            font-size: 11px;
            
            .form__link {
                font-size: 11px;
            }
        }
    }

    @media(max-width:1023px) {
        margin-left: 0;
        margin-top: 24px;
        text-align: center;
    }
}