* {
    font-family: "FuturaPT", sans-serif;
    font-weight: 300;
    color: $black-color;
}

body {
    background: $white-color;
}

img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 82px;

    @media(max-width:1440px) {
        padding: 0 42px;
    }

    @media(max-width:768px) {
        padding: 0 8px;
    }
}

.main-page {
    @media(min-width:1024px) {
        &::-webkit-scrollbar {
            display: none;
        }

        -ms-overflow-style: none;
        /* IE and Edge */
        scrollbar-width: none;
        /* Firefox */
    }
}

.page {
    position: relative;
    overflow: hidden;

    min-height: 100vh;
    flex-direction: column;
    display: flex;

    &.scroll-hide {
        height: calc(var(--vh, 1vh) * 100);
    }

    &.popup-open {

        .verticle-slider,
        .header__inner,
        .cases,
        .company,
        .reviews-block,
        .footer,
        .error-section,
        .contacts,
        .development,
        .price,
        .construction,
        .questions,
        .request-section,
        .case,
        .advertising,
        .projects,
        .reviews-section {
            filter: blur(3px);
        }
    }
}

.breadcrumbs {
    margin-bottom: 71px;
    position: relative;
    z-index: 5;

    &__list {
        &-item {
            display: inline-block;
            padding-right: 14px;
            position: relative;

            a,
            span {
                font-weight: 400;
                font-size: 16px;
                line-height: 32px;
                color: #FFFFFF;
                transition: all .3s;
            }

            a {
                text-decoration-line: underline;

                &:hover {
                    text-shadow: 0 0 .5px #FFFFFF;
                }
            }

            &:last-child::after {
                display: none;
            }

            &::after {
                position: absolute;
                content: '>';
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;
                text-transform: uppercase;
                color: #FFFFFF;
                top: 8px;
                right: -1px;
            }
        }
    }
}

.h1 {
    font-weight: 450;
    font-size: 100px;
    line-height: 130px;

    @media(max-width:1439px) {
        font-size: 90px;
        line-height: 90px;
    }

    @media(max-width:1023px) {
        font-size: 40px;
        line-height: 60px;
    }

    span {
        font-weight: 450;
        font-size: 100px;
        line-height: 130px;
        color: $red-color;

        @media(max-width:1439px) {
            font-size: 90px;
            line-height: 90px;
        }

        @media(max-width:1023px) {
            font-size: 40px;
            line-height: 60px;
        }

    }
}

.h2 {
    font-weight: 600;
    @include adaptive('font-size', 60, 28);
    @include adaptive('line-height', 80, 42);


    @media(min-width:1920px) {
        font-size: 60px;
        line-height: 80px;
    }

    span {
        font-weight: 600;
        color: $red-color;
        @include adaptive('font-size', 60, 28);
        @include adaptive('line-height', 80, 42);


        @media(min-width:1920px) {
            font-size: 60px;
            line-height: 80px;
        }

        @media(max-width:1439px) {
            line-height: 42px;
        }
    }
}

.body {
    font-weight: 400;
    @include adaptive('font-size', 20, 18);
    @include adaptive('line-height', 30, 26);

    @media(min-width:1920px) {
        font-size: 20px;
        line-height: 30px;
    }
}

.body2 {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
}

.body-light {
    @include adaptive('font-size', 20, 18);
    @include adaptive('line-height', 30, 26);
    font-weight: 300;

    @media(min-width:1920px) {
        font-size: 20px;
        line-height: 30px;
    }
}

.body2-light {
    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
}

.subtitle {
    font-weight: 300;
    font-size: 30px;
    line-height: 50px;
}

.subtitle1-light {
    font-weight: 300;
    @include adaptive('font-size', 36, 22);
    line-height: 54px;

    @media(min-width:1920px) {
        font-size: 36px;
        line-height: 54px;
    }

    @media(max-width:1439px) {
        line-height: 32px;
    }

    @media(max-width:1023px) {
        font-size: 22px;
        line-height: 32px;
    }
}

.subtitle1-book {
    font-weight: 400;
    font-size: 36px;
    line-height: 54px;

    @media(max-width:1023px) {
        font-size: 22px;
        line-height: 32px;
    }

    span {
        font-weight: 400;
        font-size: 36px;
        line-height: 54px;
        color: $red-color;

        @media(max-width:1023px) {
            font-size: 22px;
            line-height: 32px;
        }
    }
}

.subtitle2-light {
    font-weight: 300;
    @include adaptive('font-size', 30, 20);
    @include adaptive('line-height', 50, 30);
}

.subtitle2-book {
    font-weight: 400;
    @include adaptive('font-size', 30, 20);
    @include adaptive('line-height', 50, 30);
}

.caption2-light {
    font-weight: 300;
    font-size: 12px;
    line-height: 18px;
}