.error-page {
    .header__inner::before {
        @media(min-width:1024px) {
            height: 92px;
            box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.04);
        }
    }

    .header__logo {
        width: 119px;
        height: 68px;

        @media(max-width:1023px) {
            width: 77px;
            height: 44px;
        }
    }

    .nav {
        @media(min-width:1024px) {
            height: 68px;
            margin-left: 177px;

            @media(max-width:1320px) {
                margin-left: 65px;
            }
        }
    }

    @media (max-width: 1320px) {
        .subnav::after {
            left: 195px;
        }
    }
}

.error-section {
    width: 100vw;
    height: 100vh;

    background-image: url('../img/error-bg.svg');
    background-size: cover;
    background-repeat: no-repeat;
    overflow: hidden;
}

.error-section__inner {
    height: calc(var(--vh, 1vh) * 100);
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 136px;

    overflow: hidden;

    @media(max-width:1023px) {
        flex-direction: column;
        justify-content: flex-start;
        padding-top: 70px;
    }
}

.error-section__img {
    width: 614px;
    height: 702px;

    img {
        object-fit: contain;
    }

    /*  @media(min-width:1920px) {
        width: 214px;
        height: 202px;
    } */

    @media(max-width:1919px) {
        width: 516px;
        height: 602px;
    }

    @media(max-width:1439px) {
        width: 346px;
        height: 432px;
    }

    @media(max-width:1023px) {
        width: 50vw;
        height: auto;
    }
}

.error-section__content {
    margin-left: 50px;

    @media(max-width:1023px) {
        margin-left: 0;
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.error-section__text {
    font-weight: 400;
    font-size: 30px;
    line-height: 40px;

    margin-bottom: 12px;

    @media(max-width:1023px) {
        font-size: 26px;
        line-height: 36px;
    }
}

.error-section__title {
    font-weight: 600;
    font-size: 60px;
    line-height: 70px;

    margin-bottom: 34px;

    @media(max-width:1023px) {
        font-size: 42px;
        line-height: 42px;
        text-align: center;
    }
}