.price {
    margin-bottom: 180px;
    position: relative;

    @media(max-width:1439px) {
        margin-bottom: 100px;
    }

    @media(max-width:1023px) {
        margin-bottom: 60px;
    }
}

.price__img {
    height: 465px;
    z-index: -1;

    position: absolute;
    top: -100px;
    left: 0;
    right: 0;
    z-index: -1;

    img {
        object-fit: contain;
    }

    @media(max-width:1023px) {
        top: 64px;
        height: auto;
    }
}

.price__inner {
    display: flex;
    padding: 0 148px;

    @media(max-width:1750px) {
        padding: 0;
        justify-content: space-between;
    }

    @media(max-width:1439px) {
        align-items: center;
    }

    @media(max-width:1023px) {
        flex-direction: column;
        align-items: flex-start;
    }
}

.price__left {
    max-width: 496px;
    margin-right: 244px;


    @media(max-width:1750px) {
        margin-right: 30px;
    }
}

.price__title {
    margin-bottom: 54px;

    @media(max-width:1023px) {
        margin-bottom: 16px;

        span {
            display: block;
        }
    }

    font-weight: 600;
    @include adaptive('font-size', 60, 28);
    @include adaptive('line-height', 80, 42);

    @media(min-width:1920px) {
        font-size: 60px;
        line-height: 80px;
    }

    span {
        font-weight: 600;
        color: $red-color;
        @include adaptive('font-size', 60, 28);
        @include adaptive('line-height', 80, 42);

        @media(min-width:1920px) {
            font-size: 60px;
            line-height: 80px;
        }
    }
}

.price__value {
    font-weight: 450;
    color: $red-color;
    @include adaptive('font-size', 100, 40);
    @include adaptive('line-height', 130, 60);

    span {
        font-weight: 600;
        color: $red-color;
        @include adaptive('font-size', 40, 26);
        @include adaptive('line-height', 48, 38);
    }

    @media(max-width:1023px) {
        margin-bottom: 26px;
    }
}

.price__info {
    margin-top: 20px;

    @media(max-width:1439px) {
        margin-top: 0;
    }
}

.price__info-title {
    font-weight: 600;
    @include adaptive('font-size', 40, 26);
    @include adaptive('line-height', 48, 38);

    margin-bottom: 36px;

    @media(max-width:1023px) {
        margin-bottom: 12px;
    }
}

.price__descr {
    max-width: 572px;
    margin-bottom: 36px;

    @media(max-width:1023px) {
        max-width: unset;
        margin-bottom: 40px;
    }
}