.cases {
    margin-top: 218px;
    margin-bottom: 180px;

    position: relative;

    @media(max-width:1439px) {
        margin-top: 170px;
        margin-bottom: 60px;
    }

    @media(max-width:1023px) {
        margin-top: 90px;
    }

    @media(max-width:600px) {
        margin-bottom: 20px;
    }
}

.cases__title {
    max-width: 1231px;
    margin-bottom: 80px;

    @media(max-width:1023px) {
        margin-bottom: 36px;
    }
}

.cases__text {
    font-weight: 600;
    font-size: 250px;
    line-height: 260px;
    color: $whiteDarker-color;

    position: absolute;
    top: -34px;
    right: 82px;
    z-index: -1;

    @media(min-width:1920px) {
        right: unset;
        left: 576px;
    }

    @media(max-width:1439px) {
        font-size: 130px;
        line-height: 220px;
        right: 42px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.cases__list {
    max-width: 1739px;
}

.cases__item {
    display: block;
    max-width: calc(100% / 3 - 20px);
    margin-bottom: 20px;
    overflow: hidden;
    position: relative;
    cursor: pointer;

    @media(max-width:1023px) {
        max-width: calc(100% / 2 - 20px);
    }

    @media(max-width:600px) {
        max-width: 100%;
        margin-bottom: 40px;
    }

    &:hover {
        @media(min-width:1024px) {
            .cases__info {
                opacity: 1;
            }
        }
    }
}

.cases__item--photo {

    &:nth-child(3n+1) {
        height: 598px;
        width: 572px;

        @media(max-width:1439px) {
            height: 448px;
        }

        @media(max-width:1023px) {
            width: calc(100% / 2);
        }

        @media(max-width:600px) {
            width: 100%;
            height: 375px;
        }
    }

    &:nth-child(3n+2) {
        height: 404px;
        width: 572px;

        @media(max-width:1439px) {
            height: 254px;
        }

        @media(max-width:1023px) {
            width: calc(100% / 2);
        }

        @media(max-width:600px) {
            width: 100%;
        }
    }

    &:nth-child(3n+3) {
        height: 598px;
        width: 424px;

        @media(max-width:1439px) {
            height: 448px;
        }

        @media(max-width:1023px) {
            width: calc(100% / 2);
        }

        @media(max-width:600px) {
            width: 100%;
            height: 506px;
        }
    }

    &:nth-child(1n+11) {
        display: none;
    }
}

.case-one {
    .cases__item--photo:nth-child(10) {
        height: 404px;
        width: 572px;
    }
}

.cases__info {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    background: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));

    padding: 122px 0 0 52px;

    @media(min-width:1024px) {
        opacity: 0;
        transition: .2s linear;
        transition: .2s linear;
    }

    @media(max-width:1439px) {
        padding: 20px 6px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }
}


.cases__name {
    font-weight: 600;
    @include adaptive('font-size', 40, 26);
    @include adaptive('line-height', 48, 38);
    color: $white-color;

    margin-bottom: 12px;
}

.cases__descr {
    max-width: 330px;
    color: $white-color;
}

.cases__item--btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 572px;
    height: 210px;

    @media(max-width:1439px) {
        height: 175px;
    }

    @media(max-width:1023px) {
        height: 95px;
        align-items: flex-end;
        left: 50% !important;
        transform: translateX(-50%);
    }

    @media(max-width:600px) {
        height: 84px;
    }
}

.cases__item--stats {
    width: 424px;
    height: 404px;
    padding-top: 107px;
    padding-left: 43px;

    @media(max-width:1439px) {
        height: 254px;
        padding: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    @media(max-width:1023px) {
        width: calc(100% / 2);
    }

    @media(max-width:600px) {
        width: 100%;
    }
}

.cases__item-img {
    width: 100%;
    max-width: 338px;
    height: 120px;
    margin-bottom: 16px;

    img {
        object-fit: contain;
        object-position: center;
    }

    @media(max-width:767px) {
        width: 243px;
        height: 86px;
    }
}

.cases-two {
    margin-top: 0;

    .container {
        position: relative;
    }

    .cases__text {
        width: 1294px;
        height: 260px;
        top: 80px;
        left: 506px;

        img {
            object-fit: unset;
        }

        @media(max-width:1850px) {
            left: 20%;
            top: 4%;
            width: 75vw;
        }

        @media(max-width:1439px) {
            top: 0;
        }
    }

    .cases__item--photo {
        &:nth-child(n+10) {
            display: none;
        }
    }

    .cases__item--btn {
        width: 572px;
        height: 174px;
    }

    @media(max-width:600px) {
        .cases__item--photo:nth-child(1n + 6) {
            display: none;
        }

        .cases__item--stats {
            height: 130px;
        }

        .cases__item--btn {
            height: 84px;
        }
    }
}

.cases-slide {



    .cases__item--btn {
        width: 572px;
        height: 194px;
    }

    @media(max-width:600px) {
        .cases__item--info:nth-child(1n + 6) {
            display: none;
        }

        .cases__item--stats {
            height: 130px;
        }

        .cases__item--btn {
            height: 84px;
        }
    }
}