.calculate {
    display: flex;
    margin-left: 148px;

    position: relative;
    z-index: 2;

    @media(max-width:1700px) {
        margin-left: 0;
    }

    @media(max-width:1439px) {
        flex-direction: column;
    }

    @media(max-width:1023px) {
        margin-bottom: 174px;
    }
}

.calculate__left {
    display: flex;
    align-items: flex-start;

    @media(max-width:1023px) {
        margin-left: 26px;
    }
}

.calculate__number {
    width: 420px;
    height: 300px;
    margin-right: 26px;

    @media(max-width:1439px) {
        width: 204.44px;
        height: 153px;
        margin-right: 23px;

        img {
            object-fit: contain;
        }
    }

}

.calculate__procent {
    width: 156px;
    height: 116px;

    @media(max-width:1439px) {
        width: 79.55px;
        height: 59.16px;
    }
}

.calculate__right {
    margin-top: 54px;
    margin-left: 138px;

    @media(max-width:1439px) {
        margin-left: 0;
        margin-top: 36px;
    }
}

.calculate__text {
    max-width: 588px;
    margin-bottom: 40px;

    span {
        color: $red-color;
    }

    @media(max-width:414px) {
        max-width: 300px;
    }
}

.calculate__btn {
    @media(max-width:414px) {
        margin: 0 auto;
    }
}

.calculate__bg {
    display: flex;

    position: absolute;
    left: -120px;
    bottom: 0;
    top: 137px;
    z-index: 0;

    @media(max-width:1023px) {
        top: -115px;
        left: 0;
        right: 0;
        flex-direction: column;
        align-items: center;
    }
}

.calculate__bg-img {
    width: 1201px;
    height: 100%;

    img {
        object-fit: unset;
    }

    @media(max-width:1023px) {
        width: 359px;
        height: 269px;

        &+& {
            margin-top: 60px;
        }
    }
}

.calculate__intro {
    display: flex;
    justify-content: space-between;
    width: 100%;

    z-index: 2;

    @media(max-width:1919px) {
        padding-top: 100px;
        padding-bottom: 150px;
    }

    @media(max-width:1439px) {
        padding-top: 50px;
        padding-bottom: 80px;
    }

    @media(max-width:1023px) {
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        height: 100%;
        padding-bottom: 10px;

        position: relative;
        overflow: hidden;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

        -ms-overflow-style: none;
        /* IE and Edge */
        scrollbar-width: none;
        /* Firefox */
    }

    &.hide {
        display: none;
    }
}

.calculate__intro-left {
    @media(max-width:1023px) {
        margin-bottom: 44px;
    }
}

.calculate__intro-title {
    margin-bottom: 24px;

    @media(max-width:1023px) {
        margin: 0 auto 24px;
        text-align: center;
    }

    span {
        color: $red-color;
    }
}

.calculate__intro-list {
    margin-bottom: 24px;
}

.calculate__intro-item {
    padding-left: 30px;

    position: relative;

    &::before {
        content: '';
        width: 5px;
        height: 5px;
        border-radius: 50%;
        background-color: $black-color;

        position: absolute;
        top: 50%;
        left: 10px;
        transform: translateY(-50%);
    }
}

.calculate__intro-subtitle {
    margin-bottom: 40px;

    @media(max-width:1023px) {
        margin: 0 auto 40px;
    }
}

.calculate__intro-btn {
    @media(max-width:1023px) {
        margin: 0 auto;
    }
}

.calculate__intro-right {
    max-width: 424px;
    margin-right: 148px;

    @media(max-width:1540px) {
        margin-right: 78px;
        margin-left: 25px;
        max-width: 370px;
    }

    @media(max-width:1439px) {
        margin-right: 0;
    }

    @media(max-width:1023px) {
        margin-left: 0;
    }
}

.calculate__intro-text {
    margin-bottom: 24px;
}

.calculate__intro-descr {
    margin-bottom: 24px;
    padding-bottom: 24px;

    position: relative;

    &::before {
        content: '';
        height: 1px;
        width: 572px;
        background-color: $black-color;

        position: absolute;
        left: 0;
        bottom: 0;

        @media(max-width:1023px) {
            left: 50%;
            transform: translateX(-50%);
        }
    }
}

.calculate__intro-name {
    margin-bottom: 4px;
}

.calculate__intro-position {
    color: $red-color;
}

.calculate__intro-arrow {
    width: 309px;
    height: 215px;

    top: 85px;
    right: 14px;
    position: absolute;
    z-index: -1;

    @media(max-width:1919px) {
        width: 198px;
        height: 135px;
        top: 34px;
        right: 25px;
    }

    @media(max-width:1439px) {
        width: 128px;
        height: auto;
        top: 16px;
        right: 53px;

        img {
            object-fit: contain;
        }
    }

    @media(max-width:1023px) {
        top: 160px;
        right: 50%;
        transform: translateX(50%);

        width: 249px;
        height: 174px;
    }
}

.calculate__intro-phrase {
    height: 259px;

    position: absolute;
    right: 40px;
    left: 40px;
    bottom: 58px;
    z-index: -1;

    @media(min-width:1921px) {
        height: auto;
    }

    @media(max-width:1919px) {
        height: 192px;
        width: auto;
        right: unset;
        left: 20px;
        bottom: 25px;
        z-index: -1;

        img {
            object-fit: contain;
        }
    }

    @media(max-width:1439px) {
        bottom: 0;
        height: 150px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}