.projects {
    margin-bottom: 180px;

    @media(max-width:1439px) {
        margin-bottom: 120px;
    }

    @media(max-width:1023px) {
        margin-bottom: 60px;
    }

    @media(max-width:650px) {
        .container {
            padding: 0;
        }
    }
}

.projects__title {
    margin-bottom: 40px;

    @media(max-width:650px) {
        padding-left: 8px;
    }
}

.projects__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    @media(max-width:1439px) {
        flex-direction: column;
        align-items: center;
    }
}



.projects__slider {
    width: 1164px;
    margin: 0;

    @media(max-width:1250px) {
        width: 100%;
    }

    @media(max-width:1023px) {
        margin: 0 -8px;
    }

    @media(max-width:650px) {
        overflow: visible;
    }
}

.projects__slider-wrapper {
    margin-bottom: 20px;
    height: auto;

    @media(max-width:650px) {
        margin-bottom: 30px;
    }
}

.projects__slide {
    display: block;
    position: relative;

    &:hover {
        @media(min-width:1024px) {
            .projects__info {
                opacity: 1;
            }
        }
    }

    @media(max-width:650px) {
        transition: transform .2s linear;

        &.swiper-slide-active {
            transform: scale(1.04);
        }
    }
}

.projects__info {
    padding: 94px 48px 48px;

    background: rgba(0, 0, 0, 0.4);
    transition: .2s linear;

    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    @media(min-width:1024px) {
        opacity: 0;
    }

    @media(max-width:1023px) {
        padding: 30px 6px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }
}

.projects__name {
    margin-bottom: 12px;

    font-weight: 600;
    font-size: 40px;
    line-height: 48px;
    color: $white-color;

    @media(max-width:1023px) {
        font-size: 26px;
        line-height: 38px;
    }
}

.projects__text {
    max-width: 346px;
    color: $white-color;

    @media(max-width:1023px) {
        font-size: 18px;
        line-height: 26px;
    }
}

.projects__slider-nav {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    @media(max-width:650px) {
        justify-content: center;
    }
}

.projects-button-prev,
.projects-button-next {
    width: 28px;
    height: 28px;

    cursor: pointer;
    position: relative;

    &::before {
        content: '';
        width: 21px;
        height: 19px;

        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
}

.projects-button-prev::before {
    left: 1px;
    background-image: url('../img/arrow-left.svg');
    background-size: 100% 100%;
}

.projects-button-next::before {
    right: 1px;
    background-image: url('../img/arrow-right.svg');
    background-size: 100% 100%;
}

.projects__slider-pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    width: auto !important;
    margin: 0 34px;

    .swiper-pagination-bullet {
        width: 6px;
        height: 6px;
        margin: 0 8px !important;
        opacity: 1;
        background-color: $grey-color;

        position: relative;

        &::before {
            content: '';
            width: 12px;
            height: 12px;

            border-radius: 50%;
            background-color: $red-color;
            opacity: 0;
            transition: .2s linear;

            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    .swiper-pagination-bullet-active::before {
        opacity: 1;
    }
}

.projects__stats {
    width: 572px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @media(max-width:1439px) {
        margin-top: 40px;
    }
}

.projects__stats-img {
    width: 338px;
    height: 120px;
    margin-bottom: 18px;

    img {
        object-fit: contain;
    }

    @media(max-width:767px) {
        width: 243px;
        height: 86px;
        margin-bottom: 12px;
    }
}

.projects__stats-title {
    margin-bottom: 126px;

    @media(max-width:1439px) {
        margin-bottom: 40px;
    }
}