.includ {
    @media(max-width:1023px) {
        margin-top: 60px;
        margin-bottom: 36px;
    }

    &--development {
        margin-top: 0;
        margin-bottom: 180px;

        @media(max-width:1439px) {
            margin-bottom: 120px;
        }

        @media(max-width:1023px) {
            margin-bottom: 60px;
        }
    }
}

.includ__title {
    position: relative;
    margin-bottom: 80px;

    @media(max-width:1840px) {
        margin-bottom: 30px;
    }

    @media(max-width:1023px) {
        margin-bottom: 36px;
    }

    span {
        color: $red-color;
    }

    &--development {
        margin-bottom: 40px;
    }
}

.includ__list {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    margin: 0 -84px -56px;
    max-width: 1900px;

    @media(max-width:1830px) {
        max-width: 1600px;
        margin: 0 -20px -26px;
    }

    @media(max-width:1023px) {
        flex-direction: column;
        margin: 0;
    }
}

.includ__item {
    width: 100%;
    max-width: 424px;
    margin: 0 84px 56px;

    @media(max-width:1830px) {
        max-width: 380px;
        margin: 0 20px 26px;
    }

    @media(max-width:1439px) {
        margin: 0px 16px 20px;
        max-width: 430px;
    }

    @media(max-width:1023px) {
        width: 100%;
        max-width: unset;
        margin: 0 0 24px 0;
    }

    &--big {
        max-width: 720px;

        .includ__item-title::before {
            width: 385px;

            @media(max-width:1023px) {
                width: 89px;
            }
        }

        @media(max-width:1023px) {
            width: 100%;
            max-width: unset;
        }
    }
}

.includ__item-title {
    padding-bottom: 24px;
    margin-bottom: 24px;
    text-align: end;

    position: relative;

    @media(max-width:1439px) {
        padding-bottom: 12px;
        margin-bottom: 12px;
        font-size: 18px;
        line-height: 26px;
    }

    @media(max-width:1023px) {
        padding-bottom: 18px;
        margin-bottom: 18px;
    }

    &::before {
        content: '';
        height: 1px;
        width: 89px;
        background-color: $black-color;

        position: absolute;
        left: 0;
        bottom: 0;
    }
}

.includ__item-text {
    @media(max-width:1439px) {
        font-size: 16px;
        line-height: 24px;
    }
}

.includ__text {
    color: $whiteDarker-color;
    font-weight: 600;
    font-size: 270px;
    line-height: 260px;

    position: absolute;
    left: -82px;
    bottom: -25px;
    z-index: -1;


    @media(max-width:1830px) {
        font-size: 170px;
        left: -42px;
        bottom: -55px;
    }

    @media(max-width:1440px) {
        font-size: 140px;
    }

    @media(max-width:1439px) {
        display: none;
    }
}