.questions {
    margin-bottom: 180px;

    @media(max-width:1439px) {
        margin-bottom: 120px;
    }

    @media(max-width:1023px) {
        margin-bottom: 60px;
    }
}


.questions__inner {
    max-width: 1016px;
    margin: 0 auto;
}

.questions__title {
    margin-bottom: 60px;

    @media(max-width:767px) {
        margin-bottom: 16px;
    }

    @media(max-width:375px) {
        font-size: 28px;
        line-height: 42px;
    }
}


.questions__item {
    width: 100%;
    padding-bottom: 20px;

    border-bottom: 1px solid $grey-color;

    @media(max-width:767px) {
        padding-bottom: 24px;
    }

    &+& {
        margin-top: 36px;

        @media(max-width:767px) {
            margin-top: 24px;
        }
    }
}

.questions__top {
    cursor: pointer;
    position: relative;

    @media(max-width:767px) {
        padding-right: 63px;
    }

    &.active {
        .questions__text {
            font-weight: 400;
        }

        .questions__btn::after {
            display: none;
        }
    }
}



.questions__btn {
    display: block;
    width: 16px;
    height: 16px;

    position: absolute;
    top: 6px;
    right: 10px;

    &::before {
        content: '';
        position: absolute;
        height: 2px;
        background-color: $black-color;

        top: 50%;
        left: 0;
        right: 0;
        transform: translateY(-50%);
    }

    &::after {
        content: '';
        position: absolute;
        width: 2px;
        background-color: $black-color;

        top: 0;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
    }
}

.questions__descr {
    display: none;
    max-width: 864px;
    margin: 20px 0 0 38px;
}