.nav {
    height: 88px;
    margin-left: 144px;

    // transition: .2s linear;

    @media(min-width:1024px) {
        display: block !important;
        transition: .2s linear;
    }

    @media(min-width:1024px) {
        order: 2;
    }

    @media(max-width:1320px) {
        margin-left: 65px;
    }

    @media(max-width:1023px) {
        display: none;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-start;
        // max-height: calc((var(--vh, 1vh) * 100) - 56px);
        height: calc(100vh - 56px);
        max-height: calc(100vh - 56px);

        padding-top: 30px;
        padding-bottom: 30px;
        margin-left: 0;

        position: absolute;
        top: 100%;
        left: 0;
        right: 0;

        background-color: $white-color;

        overflow-y: scroll;
        transition: none;

        &::-webkit-scrollbar {
            display: none;
        }

        -ms-overflow-style: none;
        /* IE and Edge */
        scrollbar-width: none;
        /* Firefox */

        &.active {
            display: flex !important;
        }
    }
}

.nav__list {
    display: flex;
    align-items: center;
    height: 100%;

    @media(max-width:1023px) {
        width: 100%;
        height: auto;
        order: 2;
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 36px;
    }
}

.nav__item {
    display: flex;
    align-items: center;
    height: 100%;

    @media(max-width:1023px) {
        width: 100%;
        padding-left: 10px;
        flex-wrap: wrap;
        height: auto;
    }

    &+& {
        margin-left: 44px;

        @media(max-width:1320px) {
            margin-left: 30px;
        }

        @media(max-width:1023px) {
            margin-left: 0;
            margin-top: 20px;
            padding-top: 20px;

            border-top: 1px solid $grey-color;
        }
    }



    &--subnav {
        position: relative;

        @media(min-width:1024px) {
            &::before {
                content: '';
                width: 124px;
                height: 50px;
                background-color: #fff;
                box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.06);
                opacity: 0;
                transition: .22s linear;

                position: absolute;
                left: -27px;
                cursor: pointer;
            }

            &.active {
                .nav__item-arrow {
                    transform: rotate(180deg);
                }

                &::before {
                    opacity: 1;
                }
            }
        }

        @media(max-width:1023px) {
            position: relative;

            &::before {
                content: '';
                height: 2px;
                width: 14px;
                background-color: $black-color;

                position: absolute;
                top: 12px;
                right: 15px;
            }

            &::after {
                content: '';
                height: 14px;
                width: 2px;
                background-color: $black-color;

                position: absolute;
                top: 6px;
                right: 21px;
            }

            &.active::after {
                opacity: 0;
            }
        }
    }
}


.nav__item-arrow {
    display: block;
    width: 9px;
    height: 6px;

    background-image: url("../img/arrow.svg");
    background-size: 100% 100%;
    pointer-events: none;

    position: absolute;
    top: 45%;
    right: 9px;
    z-index: 2;

    transition: .25s linear;

    @media(max-width:1023px) {
        display: none;
    }
}

.nav__link {
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;

    padding: 10px 0;

    transition: .2s linear;

    @media(max-width:1023px) {
        padding: 0;
    }
}

.nav__link--subnav {
    padding-right: 28px;

    position: relative;
    z-index: 3;
    pointer-events: none;
}