.quiz {
    width: 100%;
    height: 100%;
    display: none;

    @media(max-width:1023px) {
        overflow: hidden;
        overflow-y: scroll;

        &::-webkit-scrollbar {
            display: none;
        }

        -ms-overflow-style: none;
        /* IE and Edge */
        scrollbar-width: none;
        /* Firefox */
    }

    &.show {
        display: flex;
    }
}

.quiz__item {
    display: none;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    width: 100%;

    position: relative;
    z-index: 2;

    @media(max-width:1919px) {
        min-height: 600px;
        padding-top: 50px;
        padding-bottom: 20px;
    }

    @media(max-width:1439px) {
        min-height: 486px;
        padding: 0;
    }

    @media(max-width:1023px) {
        flex-direction: column-reverse;
        justify-content: flex-end;
        align-items: center;
    }

    &.show {
        display: flex;

        animation-name: fade;
        animation-duration: .3s;
        animation-timing-function: linear;
    }
}

@keyframes fade {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.quiz__question {
    margin-bottom: 24px;
}

.quiz__radio {
    display: block;

    /* @media(max-width:1023px) {
        margin-left: 40px;
    } */

    &+& {
        margin-top: 14px;
    }
}

.quiz__radio-input {
    display: none;

    &:checked+.quiz__radio-text {
        &::after {
            opacity: 1;
        }

        &:hover::before {
            border: 2px solid $black-color;
        }
    }
}

.quiz__radio-text {
    padding-left: 44px;
    cursor: pointer;

    position: relative;

    &::before {
        content: '';
        border-radius: 50%;
        border: 2px solid $black-color;

        transition: .2s linear;

        width: 16px;
        height: 16px;

        position: absolute;
        top: 50%;
        left: 4px;
        transform: translateY(-50%);
    }

    &::after {
        content: '';
        width: 8px;
        height: 8px;

        border-radius: 50%;
        background-color: $black-color;
        opacity: 0;

        transition: .2s linear;

        position: absolute;
        top: 50%;
        left: 8px;
        transform: translateY(-50%);
    }

    &:hover::before {
        border: 2px solid $red-color;
    }

    &.no-decid {
        color: $grey-color;

        &::before {
            border: 2px solid $grey-color;
        }
    }
}

.quiz__btn {
    margin-top: 40px;

    @media(max-width:1023px) {
        margin: 40px auto 44px;
    }
}

.quiz__item-right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;

    @media(max-width:1023px) {
        height: auto;

        &--step4 {
            .quiz__discount-box {
                width: 119px;
            }
        }

        &--step5 {
            .quiz__discount-box {
                width: 113px;
            }
        }
    }
}

.quiz__discount {
    margin-right: 212px;

    @media(max-width:1919px) {
        margin-bottom: 16px;
    }

    @media(max-width:1023px) {
        margin-right: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.quiz__discount-title {
    margin-bottom: 24px;

    @media(max-width:1023px) {
        margin-bottom: 16px;
        margin-right: auto;
    }
}

.quiz__discount-box {
    display: flex;
    align-items: flex-start;

    @media(max-width:1440px) {
        width: 10vw;
    }

    @media(max-width:1023px) {
        width: 75px;
    }

}

.quiz__discount-number {
    margin-right: 36px;

    @media(max-width:1023px) {
        margin-right: 10px;
    }
}

.quiz__info {
    max-width: 424px;
    margin-left: auto;
    padding-right: 34px;
    padding-bottom: 24px;

    border-bottom: 1px solid $black-color;

    @media(max-width:1023px) {
        padding-right: 0;
        padding-bottom: 24px;
        margin-bottom: 0;
    }
}

.quiz__info-title {
    max-width: 319px;

    @media(max-width:1023px) {
        max-width: unset;
        margin-bottom: 24px;
    }
}

.quiz__info-text span {
    font-size: 16px;
    line-height: 24px;
    font-weight: 450;
    color: $red-color;

    @media(max-width:1023px) {
        color: #111A21;
        text-decoration: underline;
        font-weight: 400;
    }
}

.quiz__step {
    display: flex;
    align-items: center;

    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: -1;

    @media(max-width:1023px) {
        top: 124px;
        height: fit-content;
    }
}

.quiz__step-number {

    @media(max-width:1440px) {
        width: 20vw;
        height: auto;

        img {
            object-fit: contain;
        }
    }

    &--one {
        margin-right: 140px;
        margin-left: 148px;

        @media(max-width:1440px) {
            width: 8vw;
            height: auto;
            margin-right: 100px;
        }

        @media(max-width:1023px) {
            width: 130.55px;
            height: 465px;
            margin-right: 111px;
            margin-left: 0;
        }
    }

    &--two {
        margin-right: 70px;

        @media(max-width:1023px) {
            width: 200.53px;
            height: 350px;
            margin-right: 41px;
        }
    }

    &--three {
        margin-right: 95px;

        @media(max-width:1023px) {
            width: 211.53px;
            height: 360px;
            margin-right: 50px;
        }
    }

    &--four {
        margin-right: 89px;

        @media(max-width:1023px) {
            width: 230.39px;
            height: 326px;
            margin-right: 49px;
        }
    }
}

.quiz__step-text {
    @media(max-width:1440px) {
        width: 10vw;
        height: auto;

        img {
            object-fit: contain;
        }
    }

    @media(max-width:1023px) {
        height: 216.33px;
        width: 87.84px;
    }
}

.quiz__input {
    display: block;
    width: 410px;
    height: 42px;
    padding: 9px 20px;

    background: $whiteDarker-color;
    border: 0.8px solid $grey-color;
    border-radius: 0;

    @media(max-width:1023px) {
        width: 359px;
    }

    &:focus {
        font-weight: 400;

        &::placeholder {
            font-weight: 400;
        }
    }

    &::placeholder {
        font-weight: 300;
    }

    &.error {
        font-weight: 400;
        color: $red-color;

        border: 1px solid $red-color;

        &::placeholder {
            font-weight: 400;
            color: $red-color;
        }

        &:hover::placeholder {
            font-weight: 400;
            color: $red-color;
        }
    }

    &:hover::placeholder {
        font-weight: 400;

        color: #716B6D;
    }
    
    &--email {
        display: none;
    }

    &--step3 {
        margin-top: 24px;

        &.no-active {
            pointer-events: none;
        }
    }

    &--step4 {
        margin-bottom: 16px;

        &:last-child {
            margin-bottom: 24px;
        }
    }

    &--finish {
        &+& {
            margin-top: 16px;
        }
    }
}

.quiz__finish {
    display: none;
    align-items: center;
    justify-content: space-between;
    margin-right: 148px;
    width: 100%;

    @media(max-width:1919px) {
        padding-top: 50px;
        padding-bottom: 80px;
    }

    @media(max-width:1439px) {
        padding-top: 30px;
        padding-bottom: 60px;
        margin-right: 0;
        height: 100%;
    }

    @media(max-width:1023px) {
        flex-direction: column-reverse;
        align-items: center;
        justify-content: flex-end;
    }

    &.show {
        display: flex;

        animation-name: fade;
        animation-duration: .3s;
        animation-timing-function: linear;
    }
}

.quiz__finish-text {
    max-width: 424px;
    margin-bottom: 16px;

    @media(max-width:1023px) {
        margin-bottom: 24px;
    }
}

.quiz__finish-descr {
    width: 424px;
    margin-bottom: 24px;
    padding-bottom: 24px;

    position: relative;

    @media(max-width:500px) {
        width: auto;
    }

    &::before {
        content: '';
        height: 1px;
        width: 572px;
        background-color: $black-color;

        position: absolute;
        right: 0;
        bottom: 0;

        @media(max-width:1023px) {
            right: 50%;
            transform: translateX(50%);
        }
    }
}

.quiz__finish-title {
    margin-bottom: 24px;
}

.quiz__finish-left {
    @media(max-width:1023px) {
        margin-bottom: 20px;
    }

    @media(max-width:500px) {
        display: flex;
        flex-direction: column;
    }
}

.quiz__finish-right {
    @media(max-width:1023px) {
        display: flex;
        flex-direction: column;
    }
}

.quiz__item-left {
    @media(max-width:1439px) {
        @media(min-width:1024px) {
            margin-right: 20px;
        }
    }
}

.quiz__finish-group {
    display: block;

    &+& {
        margin-top: 30px;
        margin-bottom: 40px;

        @media(max-width:1023px) {
            margin-top: 15px;
        }
    }
}

.quiz__finish-agreement {
    max-width: 326px;
    margin-top: 16px;

    @media(max-width:1023px) {
        text-align: center;
        margin: 0 auto 44px;
    }

}

.quiz__finish-link {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: $red-color;
}

.quiz__finish-phrase {
    width: 814px;
    height: 91.18px;

    position: absolute;
    left: 54px;
    bottom: 54px;
    z-index: -1;

    @media(max-width:1919px) {
        left: 20px;
        bottom: 4px;
    }

    @media(max-width:1599px) {
        width: 684px;
        height: 68.18px;
        bottom: 20px;

        img {
            object-fit: contain;
            object-position: left;
        }
    }

    @media(max-width:1023px) {
        display: none;
    }

    @media(max-width:1023px) {
        right: 54px;
        bottom: 20px;
        width: auto;
        height: auto;

        img {
            object-fit: contain;
        }
    }

    @media(max-width:496px) {
        bottom: 5px;
    }
}

.quiz__finish-btn {
    margin-bottom: 16px;

    @media(max-width:1023px) {
        margin: 0 auto 24px;
    }
}