.services {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100vh;
    overflow: hidden;

    position: relative;

    @media(max-width:1440px) {
    }

    @media(max-width:1023px) {
        flex-direction: column;
        padding: 0 42px;
        height: auto;
    }

    @media(max-width:768px) {
        padding: 0 8px;
    }
}

.services__nav {
    display: flex;
    margin-right: 168px;

    position: relative;


    @media(max-width:1830px) {
        margin-right: 50px;
    }

    @media(max-width:1250px) {
        margin-right: 20px;
    }

    @media(max-width:1023px) {
        margin-right: 0;
        margin-bottom: 36px;
    }
}

.services__numbers-wrapper {
    display: flex;
    height: 240px;
    overflow: hidden;

    font-weight: 300;
    font-size: 200px;
    line-height: 240px;

    position: absolute;
    top: 0;
    left: 82px;

    @media(max-width:1830px) {
        font-size: 130px;

        top: -20px;
        left: 45px;
    }

    @media(max-width:1439px) {
        font-size: 90px;
        line-height: 120px;
        left: 20px;

        height: 120px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.services__numbers-list {
    display: flex;
    flex-direction: column;
    width: 110px;

    transition: .3s linear;
}

.services__list {
    position: relative;

    @media(max-width:1023px) {
        overflow: hidden;
    }
}

.services__item {
    width: 802px;
    padding: 16px 0 16px 378px;

    cursor: pointer;
    position: relative;

    @media(max-width:1830px) {
        width: 602px;
        padding: 16px 0 16px 278px;
        cursor: pointer;
    }

    @media(max-width:1439px) {
        width: 433px;
        padding: 8px 0 8px 140px;
    }

    @media(max-width:1023px) {
        width: 359px;
        padding: 0 0 0 8px;
        z-index: 2;
    }

    &+& {
        margin-top: 4px;

        @media(max-width:1023px) {
            margin-top: 20px;
        }
    }

    &--highlight {
        color: #FF8422;
    }

    &:hover {
        @media(min-width:1024px) {
            color: $red-color;
        }
    }

    &::before {
        content: '';
        height: 82px;
        background-color: $whiteDarker-color;

        transition: .3s linear;

        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        transform: translate(-100%, -50%);
        z-index: -1;

        @media(max-width:1439px) {
            height: 55px;
        }

        @media(max-width:1023px) {
            height: 54px;
        }
    }

    &.active {
        font-weight: 400;

        &::before {
            transform: translate(0, -50%);
        }
    }
}

.services__info-item {
    display: none;

    &.active {
        display: block;
    }

    @media(max-width:1023px) {
        padding: 0 8px;
    }

    @media(max-width:414px) {
        .btn {
            margin: 0 auto;
        }
    }
}

.services__info-title {
    margin-bottom: 40px;

    @media(max-width:1439px) {
        margin-bottom: 12px;
    }
}

.services__info-text {
    max-width: 720px;
    margin-bottom: 40px;

    @media(max-width:1250px) {
        max-width: 540px;
        line-height: 26px;
    }

    @media(max-width:1023px) {
        max-width: unset;
    }
}

.services__big-num {
    position: absolute;
    top: 78%;
    left: 82px;
    z-index: -1;

    font-weight: 300;
    font-size: 600px;
    line-height: 1px;
    color: $whiteDarker-color;

    @media(max-width:1830px) {
        font-size: 400px;
    }

    @media(max-width:1440px) {
        font-size: 260px;
        line-height: 300px;
        left: 42px;
        top: 64%;
    }

    @media(max-width:1023px) {
        display: none;
    }
}