.verticle-slider {
    position: relative;


    .swiper-wrapper {
        width: 100vw;
        height: 100vh;

        @media(max-width:1023px) {
            height: auto;
            display: flex;
            flex-direction: column;
        }
    }
}

.verticle-slide {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100vw;

    background-color: $white-color;

    position: relative;

    @media(min-width:1024px) {

        &::before {
            content: '';
            background-color: rgba($color: #000000, $alpha: .7);
            transition: .7s linear;
            pointer-events: none;

            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 100;
        }

        &.swiper-slide-active {
            overflow-y: scroll !important;

            &::before {
                opacity: 0;
                transition: unset;
            }
        }

        &::-webkit-scrollbar {
            display: none;
        }

        -ms-overflow-style: none;
        scrollbar-width: none;


        &--intro {
            justify-content: center;
        }

        &--calculate {
            justify-content: center;
        }

        &--include {
            justify-content: center;

            @media(max-width:1919px) {
                .includ {
                    margin-top: 40px;
                }

                

                .includ__item-title {
                    padding-bottom: 10px;
                    margin-bottom: 10px;
                }

                .includ__item {
                    margin-bottom: 16px;
                }

                .includ__text {
                    line-height: 160px;
                    bottom: 5px;    
                }
            }

            @media(max-width:1439px) {
                .includ__item-text {
                    font-size: 15px;
                    line-height: 22px;
                }

                .includ__item {
                    margin-bottom: 6px;
                }
            }

            @media(max-width:1365px) {
                .includ__title {
                    font-size: 35px;
                    line-height: 42px;

                    span {
                        font-size: inherit;
                        line-height: 42px;
                    }
                }
            }
        }

        /* 
        &--services {
            justify-content: center;
        } */

        &--reviews {
            justify-content: center;

            .reviews {
                height: 100vh;
                align-items: center;

                position: relative;
            }

            @media(max-width:1919px) {
                .reviews__phrase {
                    bottom: 32px;
                }

                .reviews__img {
                    width: 256px;
                    height: 330px;
                }

                .reviews__title {
                    margin-bottom: 20px;
                    line-height: 62px;

                    span {
                        line-height: inherit;
                        font-size: inherit;
                    }
                }
            }

            @media(max-width:1439px) {
                .reviews__img {
                    width: 228px;
                    height: 290px;
                }

                .reviews__title {
                    margin-bottom: 16px;
                    line-height: 52px;
                }

                .reviews__name {
                    margin-bottom: 0;
                }

                .reviews__text {
                    margin-bottom: 20px;
                }
            }


            @media(max-width:1365px) {
                .reviews__img {
                    width: 160px;
                    height: 210px;
                    margin-left: auto;
                    margin-right: auto;
                }

                .reviews__title {
                    line-height: 42px;
                    font-size: 35px;
                }

                .reviews__text {
                    font-size: 18px;
                }

                .reviews__descr {
                    font-size: 14px;
                    line-height: 19px;
                    text-align: center;
                }

                .reviews__phrase {
                    bottom: 82px;
                }
            }
        }
    }

    &--team {
        justify-content: center;

        @media(max-width:1919px) {
            .team__img {
                height: 300px;
            }
        }

        @media(max-width:1439px) {
            .team__title {
                margin-bottom: 0;
            }

            .team__subtitle {
                margin-bottom: 20px;
                font-size: 26px;
                max-width: 975px;
            }

            .team__img {
                height: 270px;
            }
        }

        @media(max-width:1365px) {
            .team__title {
                font-size: 35px;
                line-height: 42px;

                span {
                    font-size: inherit;
                    line-height: 42px;
                }
            }

            .team__subtitle {
                font-size: 22px;
                max-width: 975px;
                max-width: 835px;
            }
        }
    }

    &--profesional {
        justify-content: center;

        @media(max-width:1919px) {
            .profesional__title {
                line-height: 52px;
                margin-bottom: 20px;
            }

            .profesional__list {
                margin: 0 -10px -20px;
                max-width: 660px;
            }

            .profesional__item {
                margin: 0 10px 20px;
                width: 176px;
                height: 176px;
            }
        }

        @media(max-width:1439px) {
            .profesional__list {
                max-width: 400px;
                margin: 0 -5px -10px;
            }

            .profesional__item {
                margin: 0 5px 10px;
                width: 156px;
                height: 156px;
            }

            .profesional__phrase {
                top: calc(100% + 5px);
            }
        }

        @media(max-width:1365px) {
            .profesional__title {
                font-size: 35px;
                line-height: 42px;
                margin-bottom: 8px;

                span {
                    font-size: 35px;
                    line-height: 42px;
                }
            }

            .profesional__text {
                font-size: 18px;
                max-width: 405px;
            }
        }
    }


    &--company {
        justify-content: center;
        margin-top: 30px;

        @media(max-width:1919px) {
            margin-top: 0;
        }

        @media(min-width:1024px) {
            @media(max-width:1919px) {
                .company {
                    margin-top: 60px;
                }

                .company__title {
                    line-height: 52px;
                    margin-bottom: 0;

                    span {
                        line-height: inherit;
                    }
                }

                .company__subtitle {
                    margin-bottom: 6px;
                }

                .company__item {
                    height: 150px;
                    margin-bottom: 20px;
                }

                .company__img {
                    img {
                        max-height: 90px;
                        width: 100%;
                        height: auto;
                        object-fit: contain;
                    }
                }

                .company__btn {
                    margin-top: 20px;
                }

                .company__phrase {
                    height: 227px;
                    bottom: -142px;
                }
            }

            @media(max-width:1439px) {
                .company {
                    margin-top: 40px;
                }
                .company__title {
                    line-height: 52px;
                }

                .company__list {
                    margin: 0 -40px -10px;
                }

                .company__item {
                    width: calc(25% - 80px);
                    height: 120px;
                    margin: 0 40px 10px;
                }

                .company__img {
                    img {
                        max-height: 70px;
                        width: 100%;
                        height: auto;
                        object-fit: contain;
                    }
                }

                .company__phrase {
                    height: 187px;
                    bottom: -112px;
                }

                .company__descr {
                    bottom: 5px;
                }

            }

            @media(max-width:1365px) {
                .company__title {
                    font-size: 35px;
                    line-height: 42px;

                    span {
                        font-size: inherit;
                        line-height: 42px;
                    }
                }
            }
            
            @media(max-width:1100px) {
                .company__list {
                    margin: 0 -10px -10px;
                }

                .company__item {
                    width: calc(25% - 20px);
                    height: 130px;
                    margin: 0 10px 10px;
                }

                .company__phrase {
                    height: 147px;
                    bottom: -72px;
                }
            }
        }
    }

    &--cases {
        padding-top: 218px;
        padding-bottom: 80px;

        @media(max-width:1919px) {
            padding-top: 160px;
            padding-bottom: 40px;
        }

        @media(max-width:1440px) {
            padding-top: 118px;
            padding-bottom: 30px;
        }

        @media(max-width:1365px) {
            .cases__title {
                font-size: 35px;
                line-height: 42px;

                span {
                    font-size: inherit;
                    line-height: 42px;
                }
            }
        }

        @media(max-width:1023px) {
            padding-top: 0;
            padding-bottom: 60px;
        }
    }
}

.verticle-slider__box {
    display: flex;
    flex-direction: column;
    align-items: center;

    position: absolute;
    top: 50%;
    right: 108px;
    z-index: 10;
    transform: translateY(-50%);

    @media(max-width:1919px) {
        right: 68px;
    }

    @media(max-width:1440px) {
        right: 20px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.verticle-slider__pagination {
    margin-bottom: 66px;

    position: relative;
    right: unset !important;
    top: unset !important;
    transform: unset !important;

    &::before {
        content: '';
        width: 1px;
        height: 40px;

        background-color: $grey-color;

        position: absolute;
        top: calc(100% + 12px);
        left: 0;
    }

    .swiper-pagination-bullet {
        display: block;
        width: 2px;
        height: 8px;
        margin: 6px 0 0 0;

        background-color: $grey-color;
        border-radius: unset;

        &.swiper-pagination-bullet-active {
            background-color: $red-color;
        }
    }
}

.verticle-slider__pagination2 {
    position: static;
    right: unset !important;
    top: unset !important;
    transform: unset !important;

    .swiper-pagination-bullet {
        font-weight: 450;
        font-size: 18px;
        color: $black-color;

        display: none !important;

        &.swiper-pagination-bullet-active {
            display: block !important;
            margin: 0;
            width: auto;
            height: auto;
            background-color: transparent;
        }
    }
}

.verticle-slider__counter {
    display: flex;
    align-items: flex-end;

    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
}

.verticle-slider__count {
    margin-bottom: 3px;

    font-weight: 300;
    font-size: 10px;
    color: $black-color;
}