.advertising {
    background-color: $whiteDarker-color;
    margin-bottom: 180px;

    @media(max-width:1439px) {
        margin-bottom: 120px;
    }

    @media(max-width:1023px) {
        margin-bottom: 60px;
    }
}

.advertising__inner {
    display: flex;
    width: 100%;
    justify-content: space-between;
    position: relative;
    padding: 120px 0;

    @media(max-width:1439px) {
        flex-direction: column;
        padding: 44px 0;
    }
}

.advertising__info {
    max-width: 720px;

    @media(max-width:1439px) {
        max-width: unset;
        margin-bottom: 24px;
    }
}

.advertising__title {
    margin-bottom: 30px;

    @media(max-width:1023px) {
        margin-bottom: 16px;
    }
}

.advertising__block {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 869px;
    height: 315px;

    background-color: $grey-color;

    @media(max-width:1830px) {
        max-width: 640px;
        margin-left: 20px;
    }

    @media(max-width:1439px) {
        margin: 0 auto;
    }

    @media(max-width:1023px) {
        width: 359px;
        height: 130px;
    }

    &+& {
        margin-top: 36px;
    }
}

.advertising__img {
    width: 100px;
    height: 100px;
}


.advertising__phrase {
    width: 720px;
    height: 120px;

    position: absolute;
    left: 0;
    bottom: 0;

    img {
        object-fit: unset;
    }

    @media(max-width:1439px) {
        display: none;
    }
}