.team {
    position: relative;

    @media(max-width:1023px) {
        margin-bottom: 60px;
    }
}

.team__title {
    margin-bottom: 20px;

    @media(max-width:425px) {
        margin-bottom: 16px;
        max-width: 270px;
    }
}

.team__subtitle {
    margin-bottom: 40px;
    max-width: 1406px;

    @media(max-width:1023px) {
        margin-bottom: 36px;
    }
}

.team__list {
    display: flex;
    margin: 0 -10px;

    @media(max-width:1439px) {
        margin: 0;
    }

    @media(max-width:1023px) {
        flex-direction: column;
        margin: 0 -8px;
        align-items: center;
    }
}

.team__item {
    max-width: 267px;
    width: calc(100% / 6 - 10px);
    margin: 0 10px;

    cursor: pointer;

    position: relative;

    @media(max-width:1439px) {
        margin: 0;
    }

    @media(max-width:1023px) {
        max-width: unset;
        width: 375px;
        height: 504px;
    }

    &::before {
        content: '';
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
        opacity: 1;
        transition: .2s linear;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        @media(max-width:1023px) {
            display: none;
        }
    }

    &:hover {
        @media(min-width:1024px) {
            .team__info {
                opacity: 1;
            }

            &::before {
                opacity: 0;
            }
        }
    }
}

.team__img {
    height: 370px;

    @media(max-width:1440px) {
        height: auto;
    }
}

.team__info {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 24px 14px;

    opacity: 0;
    transition: .2s linear;

    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    @media(max-width:1439px) {
        padding: 24px 5px;
    }

    @media(max-width:1023px) {
        opacity: 1;
        padding: 50px 18px;
    }
}

.team__name {
    color: $white-color;
    margin-bottom: 8px;

    @media(max-width:1439px) {
        font-size: 26px;
        line-height: 36px;
    }

    @media(max-width:1023px) {
        margin-bottom: 10px;
    }
}

.team__descr {
    color: $white-color;
    font-size: 16px;
    line-height: 22px;

    @media(max-width:1023px) {
        font-size: 20px;
        line-height: 30px;
    }
}

.team__phrase {
    position: absolute;
    top: calc(100% + 14px);
    left: 0;
    right: 0;

    @media(min-width:1921px) {
        right: unset;
        max-width: 1756px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}