.select__region {
    width: fit-content;
    padding-bottom: 2px;
    border-bottom: 1px dashed $red-color;
    cursor: pointer;

    &--btn {
        margin-left: auto;

        @media(min-width:1024px) {
            order: 3;
        }

        @media(max-width:1023px) {
            display: block;
            margin: 0 auto 36px;
            order: 1;
        }
    }
}

.select__region-text {
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
}

.region__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    max-width: 417px;

    @media(max-width:767px) {
        flex-direction: column;
        width: 187px;
    }
}

.region__item {
    margin: 0 38.5px 60px;

    position: relative;
    transition: .2s linear;
    cursor: pointer;

    @media(min-width:1920px) {
        font-size: 20px;
        line-height: 30px;
    }

    @media(max-width:767px) {
        width: fit-content;
        margin: 0 auto;

        &+& {
            margin-top: 20px;
            padding-top: 20px;

            &::after {
                content: '';
                height: 1px;
                width: 187px;
                background-image: url("../img/line.svg");

                position: absolute;
                top: 0;
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }

    &.active {
        color: $red-color;

        &::before {
            content: '';
            height: 1px;
            background-color: $red-color;

            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
        }
    }

    &:hover {
        @media(min-width:1024px) {
            color: $red-color;
        }
    }
}