.intro {
    @media(max-width:1023px) {
        margin-top: 198px;
        margin-bottom: 175px;
    }
}

.intro__title {
    margin-bottom: 40px;

    span {
        color: $red-color;
    }

    @media(max-width:1023px) {
        margin-bottom: 16px;
    }
}

.intro__subtitle {
    &+& {
        margin-top: 12px;

        @media(max-width:414px) {
            max-width: 319px;
            margin-top: 16px;
        }
    }

    span {
        color: $red-color;
        font-weight: 400;
    }
}

.intro__quote {
    width: 1460px;
    height: 184px;

    position: absolute;
    left: 296px;
    bottom: 64px;
    z-index: -1;

    @media(max-width:1788px) {
        left: 5%;
    }

    @media(max-width:1600px) {
        width: 90vw;
        height: auto;
    }

    @media(max-width:1023px) {
        width: 359px;
        height: 45px;
        top: 101px;
        z-index: 1;
        bottom: unset;
        left: 8px;
    }
}