.case__intro {
    @include adaptive('height', 919, 375);

    margin-bottom: 40px;

    @media(max-width:1023px) {
        margin-top: 56px;
        margin-bottom: 16px;
    }

    @media(max-width:375px) {
        height: 375px;
    }

    &--type-two {
        margin-bottom: 120px;
        position: relative;

        @media(max-width:1439px) {
            margin-bottom: 60px;
        }

        @media(max-width:1023px) {
            margin-bottom: 16px;
        }
    }
}

.case__intro-content {
    display: flex;
    flex-direction: column;
    justify-content: center;

    background: rgba(0, 0, 0, 0.6);

    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .case__title {
        color: $white-color;
    }

    .case__subtitle {
        max-width: 868px;
        margin-bottom: 0;
        color: $white-color;
    }
}


.case__title {
    margin-bottom: 20px;

    @media(max-width:1023px) {
        margin-bottom: 6px;
    }
}

.case__subtitle {
    margin-bottom: 80px;

    font-weight: 400;
    @include adaptive('font-size', 36, 20);
    @include adaptive('line-height', 54, 30);

    @media(max-width:1023px) {
        margin-bottom: 40px;
    }

    @media(max-width:375px) {
        font-size: 20px;
        line-height: 30px;
    }
}

.case__list--type-two {
    .case__item {
        margin-bottom: 40px;

        @media(max-width:1023px) {
            margin-bottom: 60px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

.case__item {
    padding: 60px 148px 0;
    margin-bottom: 60px;
    border-top: 1px solid rgba($black-color, .2);

    @media(max-width:1750px) {
        padding: 60px 0 0;
    }

    @media(max-width:1023px) {
        padding-top: 14px;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

.case__info {
    display: flex;
    justify-content: space-between;

    @media(max-width:1023px) {
        flex-direction: column;
    }
}

.case__name {
    font-weight: 600;
    @include adaptive('font-size', 40, 26);
    @include adaptive('line-height', 48, 38);

    @media(max-width:1023px) {
        margin-bottom: 24px;
    }
}

.case__descr {
    max-width: 868px;

    @media(max-width:1439px) {
        max-width: 670px;
    }

    @media(max-width:1023px) {
        max-width: unset;
    }
}

.case__dots-list {
    margin-top: 20px;
}

.case__dots-item {
    padding-left: 28px;

    position: relative;

    &::before {
        content: '';
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: $red-color;

        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
    }

    &+& {
        margin-top: 20px;
    }
}

.case__number-list {
    margin-top: 20px;
}

.case__number-item {
    position: relative;
    padding-left: 40px;

    &+& {
        margin-top: 44px;

        @media(max-width:1023px) {
            margin-top: 20px;
        }
    }
}

.case__number {
    color: $red-color;

    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
}

.case__examples {
    display: flex;
    justify-content: space-between;

    &--first {
        margin-top: 120px;

        @media(max-width:1023px) {
            margin-top: 60px;
        }

    }
}

.case__examples-img {
    margin-bottom: 120px;
    filter: drop-shadow(0px 4px 20px rgba(0, 0, 0, 0.12));

    @media(max-width:1023px) {
        margin-bottom: 60px;
    }

    &+& {
        margin-left: 168px;

        @media(max-width:1023px) {
            margin-left: 30px;
        }
    }
}

.full-photo {
    @media(min-width:1024px) {
        display: none;
    }

    width: 100vw;
    height: calc(var(--vh, 1vh) * 100);

    background: rgba(0, 0, 0, 0.6);
    visibility: hidden;
    opacity: 0;
    overflow: scroll;
    transition: .2s linear;

    &::-webkit-scrollbar {
        display: none;
    }

    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */

    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;

    &.active {
        visibility: visible;
        opacity: 1;
    }
}

.full-photo__inner {
    padding: 68px 8px 84px;
}

.full-photo__close {
    width: 14px;
    height: 14px;

    background-image: url('../img/close-white.svg');

    position: absolute;
    top: 35px;
    right: 25px;
}

.full-photo__wrapper {
    width: 100%;
    height: auto;

    border: 0.5px solid #111A21;

    filter: drop-shadow(0px 4px 20px rgba(0, 0, 0, 0.12));
}